import * as echarts from 'echarts';
import axios from 'axios'
import { Table } from 'antd'
import React, {useState} from "react";
import Zhe1 from './s-04-router/zhexian'
import Zhu1 from './s-04-router/zhuzhuang'
import Bing1 from './s-04-router/bingzhuang'
import './router-view.css'


let A =function(){
   const [arr,setarr] = useState([])
    //人员名单
    axios.post('http://crm.cimns.com/index.php/admin/structures/getSubUserByStructrue').then((data)=>{
      if(arr.length<=0){
         let a = [...arr]
         a = [...data.data.data]
         setarr(a)
         // console.log(a);
       }
    },(err)=>{
      console.log(err)
    })
    
// 组件切换  开关
const [val,setval] = useState('折线图')
function Qh(event){
 
   setval(event.target.value)
}
    
    let arr1=[]
   return(
      <div className="border_box">
              
      <div className="max_box">
      <div className="padin">
         <div className="max_select">
            <div className="item_select">
               <select key="a" >
                  <option key ="今天" >今天</option>
                  <option key ="昨天">昨天</option>
                  <option key="本周">本周</option>
                  <option key="上周">上周</option>
                  <option key="本月">本月</option>
                  <option key="上月">上月</option>
                  <option key="今年" selected>今年</option>
                  <option key="去年">去年</option>
                  <option key="自定义">自定义</option>
               </select>
            </div>
            <div className="item_select">
               <select key="b" >
               <option key ="办公室" selected>办公室</option>
               <option key ="打杂部">打杂部</option>
               </select>
            </div>
            <div className="item_select">
               <select key="c" >
                  
               <option value="a">选择员工</option>
               {arr.map((item,index)=>{
                  return(
                     <option key={index} value ={index}>{item.realname}</option>
                  )
               })}
               </select>
            </div>
            <div className="item_select" >
               <select key="d"  value={val} onChange={Qh} >
                  <option key ="1" style={{display:'none'}}></option>
                  <option key ="折线图" selected>折线图</option>
                  <option key ="饼状图" >饼状图</option>
                  <option key ="柱状图" >柱状图</option>
               </select>
            </div>
               <button className="btn1">搜索</button>
            
         </div>
         <div className="mains">
               <div className="tab_1" style={{opacity:val==='折线图'?"1":'0',zIndex:val==='折线图'?"99":'1'}}>
                  <Zhe1></Zhe1>
               </div>
               <div className="tab_2" style={{opacity:val==='饼状图'?"1":'0',zIndex:val==='饼状图'?"99":'1'}}>
                  <Bing1></Bing1>
               </div>
               <div className="tab_3" style={{opacity:val==='柱状图'?"1":'0',zIndex:val==='柱状图'?"99":'1'}}>
                   <Zhu1></Zhu1>
               </div>
         </div>
         <div className="tabl">

         <div className="no3_1_1_box_content_list ">
                    <div className="no3_1_1_box_content_list_top">
                        <div>客户名称</div>
                        <div>合同名称</div>
                        <div>合同金额（元）</div>
                        <div>汇款金额（元）</div>
                        <div>客户行业</div>
                        <div>客户来源</div>
                        <div>负责人</div>
                        <div>创建人</div>
                        <div>创建时间</div>
                        <div>下单时间</div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent ">
                        {arr1.map((item, index) => {
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item.realname}</div>
                                    <div>{item.customer_num}</div>
                                    <div>{item.deal_customer_num}</div>
                                    <div>{item.deal_receivables_rate}</div>
                                    <div>{item.contract_money}</div>
                                    <div>{item.receivables_money}</div>
                                    <div>{item.un_receivables_money}</div>
                                    <div>{item.un_receivables_money}</div>
                                </div>
                            )
                        })}
                    </div>
                </div>
         </div>
         <div className="ppp">
            <p>以回款金额排降序，最多展示50条客户数据</p>
         </div>
         </div>
         
      </div>
   </div>
   )
}

export default A


