import './no4.css'
import './wrap.css'
import {Component} from 'react';
import {Switch,Route,Link} from 'react-router-dom'
import { Layout, Menu } from 'antd';
import axios from 'axios'
import {
  DesktopOutlined,
  BarChartOutlined,
  AppstoreOutlined,
  PushpinOutlined,
  PrinterOutlined,
  DeleteOutlined,
} from '@ant-design/icons';
import Demo1 from '../view/demo1'
import Demo2 from '../view/demo2'
import Demo3 from '../view/demo3'
import Demo4 from '../view/demo4'
import Demo5 from '../view/demo5'
import Demo6 from '../view/demo6'
import Demo7 from '../view/demo7'
import Demo8 from '../view/demo8'

const { Content, Sider } = Layout;
const { SubMenu } = Menu;

class No4 extends Component {
  state = {
    collapsed: false,
    size: 'large',
    visible2: false,
    type:true,
    wroklist:[],
    indexlist:[]
  };
  componentWillMount(){
    axios.post('http://crm.cimns.com/index.php/work/index/workList').then((data)=>{
      // console.log(data.data.data);
      this.setState({
        wroklist : data.data.data
      })
    })
    axios.post('http://crm.cimns.com/index.php/work/tasklable/index').then((data)=>{
      // console.log(data.data.data.list);
      this.setState({
        indexlist : data.data.data.list
      })
    })
  }
    onCollapse = collapsed => {
        this.setState({ collapsed });
        this.setState({type:!this.state.type})
    };

    handleSizeChange = e => {
        this.setState({ size: e.target.value });
        
    };
    showModal = () => {
      this.setState({
        visible: true,
      });
    };
  
    hideModal = () => {
      this.setState({
        visible: false,
      });
    };
  render() {
    const { collapsed } = this.state;
    return (
      <div className='no4'>
          <Layout style={{ minHeight: '100vh',paddingTop:'70px' }}>
            <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse} className="aaaaa">
            <div className="logo">
                <Link to='/app/demo/create'>
                  <div className='flex'>
                    <p style={{display:this.state.type===true?'block':'none'}} className="create">创建项目</p>
                    <span>+</span>
                  </div>
                </Link>
            </div>
              <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
                <SubMenu key="sub1" icon={<DesktopOutlined />} title="工作台">
                    <Menu.Item key="1">
                        <Link to='/app/demo'>我的任务</Link>
                    </Menu.Item>
                    <Menu.Item key="2">
                        <Link to='/app/demo/calendar'>任务日历</Link>
                    </Menu.Item>
                </SubMenu>
                <SubMenu key="sub2" icon={<PrinterOutlined />} title="项目">
                  {this.state.wroklist.map((item,idx)=>{
                    return(
                      <Menu.Item key={item.work_id}>
                        <Link to={'/app/demo/project/' + item.work_id}>{item.name}</Link>
                      </Menu.Item>
                    )
                  })}
                </SubMenu>
                <Menu.Item key="1000001" icon={<BarChartOutlined />}>
                    <Link to='/app/demo/stats'>统计分析</Link>
                </Menu.Item>
                <Menu.Item key="4" icon={<AppstoreOutlined />}>
                    <Link to='/app/demo/items'>归档项目</Link>
                </Menu.Item>
                <SubMenu key="sub3" icon={<PushpinOutlined />} title="标签">
                  {this.state.indexlist.map((item,idx)=>{
                      return(
                        <Menu.Item key={item.lable_id}>
                          <Link to={'/app/demo/label/' + item.lable_id}>{item.name}</Link>
                        </Menu.Item>
                      )
                    })}
                </SubMenu>
                <Menu.Item key="6" icon={<DeleteOutlined />}>
                    <Link to='/app/demo/reclaim'>回收站</Link>
                </Menu.Item>
              </Menu>
            </Sider>
            <Layout className="site-layout">
              <Content style={{ margin: '0 16px' }}>
                <div className="site-layout-background" style={{ padding: '24px 0', height: '100%' , boxSizing:'border-box'}}>
                    <Switch>
                        <Route path='/app/demo' component={Demo1} exact/>
                        <Route path='/app/demo/calendar' component={Demo2} />
                        <Route path='/app/demo/stats' component={Demo3} />
                        <Route path='/app/demo/items' component={Demo4} />
                        <Route path='/app/demo/reclaim' component={Demo5} />
                        <Route path='/app/demo/create' component={Demo6} />
                        <Route path='/app/demo/project/:id' component={Demo7} />
                        <Route path='/app/demo/label/:id' component={Demo8} />
                    </Switch>
                </div>
              </Content>
            </Layout>
          </Layout>
        </div>
    );
  }
}

export default No4
