import * as echarts from 'echarts';
import React, {useState,useEffect} from "react";

let A =function(){
   useEffect (() => {
      //   柱状图
      var myChart_a = document.getElementById('main');
      var myChart_b = echarts.init(myChart_a);
      myChart_b.setOption({
       tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
              type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          }
      },
       color: ['#6ca2ff','#ff7474'],
       legend: {
         bottom:0
       },

       dataset: {
           source: [
               ['product', '客户成交数', '新增客户数'],
               ['2021-01', 0,0],
               ['2021-02', 0,0],
               ['2021-03', 0,0],
               ['2021-04', 0,0],
               ['2021-05', 0,0],
               ['2021-06', 0,0],
               ['2021-07', 0,0],
               ['2021-08', 0,0],
               ['2021-09',0,0],
               ['2021-10', 0,0],
               ['2021-11', 0,0],
               ['2021-12', 0,0]
           ]
       },
         xAxis: {type: 'category'},
         yAxis: [
              {
            type : 'value',
            axisLine:{show:true,
            lineStyle:{color:'#ccc'}
            },
            axisTick:{
            show:false,
            },
            axisLabel:{
            },
            splitNumber:5
             
            }
          ],
       series: [
           {type: 'bar'},
           {type: 'bar'},
       ]
    })
   //  柱状图结束
   })



   return(
      <div>
         <div className="zhu" id="main"></div>
      </div>
   )
}

export default A