import './demo5-frame.css'
import { useEffect , useState } from 'react'
import axios from 'axios'
import { Popover, Button, DatePicker, Space , Input , Tabs  } from 'antd';
import 'moment/locale/zh-cn';
import locale from 'antd/es/date-picker/locale/zh_CN';
import {
    PlusOutlined,
    UserOutlined,
    ClockCircleOutlined,
  } from '@ant-design/icons';
let Demo5Frame = function(props){
    const { TabPane } = Tabs;

    function callback(key) {
    console.log(key);
    }
    // console.log(props);
    const text = <span>Title</span>;
    const content = (
    <div>
        <p>Content</p>
        <p>Content</p>
    </div>
    );

    const buttonWidth = 70;
    function onChange(date, dateString) {
        console.log(date, dateString);
    }
    const { TextArea } = Input;
    function Change(e){
        console.log('Change:', e.target.value);
    }
    const [json,setjson] = useState({})
    const [list,setlist] = useState([])
    useEffect(() => {
        axios.post('http://crm.cimns.com/index.php/work/task/read',{
            task_id: props.taskId
        }).then((data)=>{
            setjson({...data.data.data})
        })
        // http://crm.cimns.com/index.php/work/task/readLoglist
        axios.post('http://crm.cimns.com/index.php/work/task/readLoglist',{
            task_id: props.taskId
        }).then((data)=>{
            setlist([...data.data.data])
        })
    }, [props.taskId])
    // console.log(list);
    const [info,setinfo] = useState(false)
    const [addchild,setaddchild] = useState(false)
    return(
        <div className="demo5-frame">
            <ul className="demo5-operate"> 
                    <li>优先级</li>
                    <li>标签</li>
                    <li>子任务</li>
                    <li>附件</li>
                </ul>
            <div className="demo5-conts">
                <div className="demo5-info">
                    该任务已于{json.update_time} 被放入回收站。<span>回复</span><span>彻底删除</span>
                </div>
                <div className="cont1 cont">
                    <div className="userName">{json.name}</div>
                    <div className="userBox">
                    <p>负责人：</p>
                        <div className="cont1-userImg">
                            <img src={json.main_user_img} alt-="" style={{display:json.main_user_img?'block':'none'}} />
                            <p style={{display:json.main_user_img?'none':'block'}}>{json.main_user_name}</p>
                        </div>
                    </div>
                </div>
                <div className="cont2 cont">
                    <div>
                    <Popover placement="right" title={text} content={content} trigger="click">
                        <p>标签</p>
                    </Popover>
                    </div>
                    <div className="time">
                        <p>截止日期：</p>
                        <div>
                            <Space direction="vertical">
                                <DatePicker onChange={Change} locale={locale} />
                            </Space>
                        </div>
                    </div>
                </div>
                <div className="cont3">
                    <div className="cont3-title" style={{display:info?'none':'block'}}>
                        <p>暂无描述</p>
                        <p className="addinfo" onClick={()=>{
                            setinfo(true)
                        }}><PlusOutlined />添加描述</p>
                    </div>
                    <div className="text-box" style={{display:info?'block':'none'}}>
                        <TextArea showCount maxLength={100} onChange={onChange} />
                        <Button type="primary" onClick={()=>{
                            setinfo(false)
                        }}>保存</Button>
                        <Button onClick={()=>{
                            setinfo(false)
                        }}>取消</Button>
                    </div>
                </div>
                <div className="cont4">
                    <p className="cont4-title">关联业务</p>
                    <div className="cont4-btn">
                        关联业务
                    </div>
                    <div></div>
                </div>
                <div className="cont5">
                    <div className="cont5-title-box">
                        <p className="title">子任务</p>
                        <div className="cont5-line">
                            <span>(0/0)</span>
                            <div className="line">
                                <div></div>
                            </div>
                        </div>
                    </div>
                    <div className="cont4-btn"  style={{display:addchild?'none':'block'}}  onClick={()=>{setinfo(false)}}>添加子任务</div>
                    <div className="input-box">
                        <Input placeholder="请输入内容" />
                        <span className="time"><ClockCircleOutlined /></span>
                        <span className="people"><UserOutlined /></span>
                    </div>
                </div>
                <div className="cont6">
                    <Tabs defaultActiveKey="1" onChange={callback}>
                        <TabPane tab="评论" key="1">
                            <div className="addBox">
                                <div className="cont1-userImg">
                                    <img src={json.main_user_img} alt-="" style={{display:json.main_user_img?'block':'none'}} />
                                    <p style={{display:json.main_user_img?'none':'block'}}>{json.main_user_name}</p>
                                </div>
                                <div className="addtext">
                                    添加评论
                                </div>
                            </div>
                        </TabPane>
                        <TabPane tab="活动" key="2">
                            {list.map((item,idx)=>{
                                return (
                                    <div className="activeCont" key={item.log_id}>
                                        <div className="left">
                                            <div className="cont1-userImg">
                                                <img src={item.thumb_img} alt-="" style={{display:item.thumb_img?'block':'none'}} />
                                                <p style={{display:item.thumb_img?'none':'block'}}>{item.realname}</p>
                                            </div>
                                            <p>{item.realname}</p>
                                            <p>{item.content}</p>
                                        </div>
                                        <div className="right">{json.create_time}</div>
                                    </div>
                                )
                            })}
                        </TabPane>
                    </Tabs>
                </div>
            </div>
            <div className="demo5-footer">
                {json.name} 创建于 {json.create_time}
            </div>
        </div>
    )
}
export default Demo5Frame