import './demo5.css'
import {useEffect,useState} from 'react'
import { Drawer, Form, Button, Col, Row, Input, Select, DatePicker } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import axios from 'axios'
import Demo5Frame from './demo5-frame.js'
import {
    ClockCircleOutlined
} from '@ant-design/icons';
  
let Demo5 = function(){
    const [list,setlist] = useState([])
    const [visible, setVisible] = useState(false);
    const { Option } = Select;
    const [taskId,settaskId] = useState()
    function showDrawer(id){
        setVisible(true);
        settaskId(id)
    }
    function onClose(){
        setVisible(false);
    }
    
    useEffect(()=>{
        axios.post('http://crm.cimns.com/index.php/work/trash/index').then((data)=>{
            // console.log(data.data.data.list);
            let newlist = [...list]
            newlist = [...data.data.data.list]
            setlist(newlist)
        })
    },[])
    // console.log(list);
    return (
        <div className="demo5"> 
            <p className="demo5-title">回收站</p>
            <div className="demo5-cont">
                <ul className="demo5-item">
                    {list.map((item,idx)=>{
                        return(
                            <li key={item.task_id}>
                                {/* {item.name} */}
                                <div onClick={()=>{
                                    showDrawer(item.task_id)
                                }}>
                                    <div className="demo5-left">
                                        {item.name}
                                    </div>
                                    <div className="demo5-right">
                                        <div style={{display:item.stop_time?'block':'none'}}>
                                            <ClockCircleOutlined />
                                            {/* {item.stop_time} */}
                                            {item.stop_time}
                                            <span>
                                                截止
                                            </span>
                                        </div>
                                        <div className="demo5-userImg" style={{display:item.main_user_name?'block':'none'}}>
                                            <img src={item.thumb_img} alt-="" style={{display:item.thumb_img?'block':'none'}} />
                                            <p style={{display:item.thumb_img?'none':'block'}}>{item.main_user_name}</p>
                                        </div>
                                    </div>
                                </div>
                            </li>
                        )
                    })}
                </ul>
                <Drawer
                // title="Create a new account"
                width={720}
                onClose={onClose}
                visible={visible}
                bodyStyle={{ paddingBottom: 80 }}
                footer={
                    <div
                    style={{
                        textAlign: 'right',
                    }}
                    >
                    <Button onClick={onClose} style={{ marginRight: 8 }}>
                        取消
                    </Button>
                    <Button onClick={onClose} type="primary">
                        确定
                    </Button>
                    </div>
                }
                >
                    <Demo5Frame taskId={taskId}></Demo5Frame>
                </Drawer>
            </div>
        </div>
    )
}
export default Demo5