import * as echarts from 'echarts';
import axios from 'axios'
import { Table } from 'antd'
import React, {useState,useEffect} from "react";
import './router-view.css'


let A =function(){
   const [arr,setarr] = useState([])

   //人员名单
   axios.post('http://crm.cimns.com/index.php/bi/customer/statistics').then((data)=>{
       if(arr.length<=0){
         let a = [...arr]
         a = [...data.data.data]
         setarr(a)
       }
    },(err)=>{
      console.log(err)
    })

       //表格
      let arrl=['2021-01','2021-02','2021-03','2021-04','2021-05','2021-06','2021-07','2021-08','2021-09','2021-10','2021-11','2021-12']

   return(
      <div className="border_box">
              
      <div className="max_box">
      <div className="padin">
         <div className="max_select">
            <div className="item_select">
               <select key="a"  >
               <option key ="volvo" >今天</option>
                        <option key ="saab">昨天</option>
                        <option key="opel">本周</option>
                        <option key="audi">上周</option>
                        <option key="opel1">本月</option>
                        <option key="audi1">上月</option>
                        <option key="opel2" selected>今年</option>
                        <option key="audi2">去年</option>
                        <option key="opel3">自定义</option>
               </select>
            </div>
            <div className="item_select">
               <select key="b" >
               <option key ="volvo" selected>办公室</option>
               <option key ="saab">打杂部</option>
               </select>
            </div>
            <div className="item_select">
               <select key="c"  >
               <option key="" disabled selected hidden>选择员工</option>
               {arr.map((item,index)=>{
                  return(
                     <option key ={index}>{item.realname}</option>
                  )
               })}
               
               
               </select>
            </div>
               <button className="btn1">搜索</button>
              
         </div>
         <div className="tabl t1">
         <p style={{color:'#333',fontSize:'16px',marginTop:'17px',marginLeft:'101px'}}>签约合同数：0个；签约合同金额：0元；回款金额：0元；未收款金额：0元</p>
         <div className="no3_1_1_box_content_list">
                    <div className="no3_1_1_box_content_list_top">
                        <div>日期</div>
                        <div>签约合同数（个）</div>
                        <div>签约合同金额（元）</div>
                        <div>回款金额（元）</div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent">
                        {arrl.map((item, index) => {
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item}</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                </div>
                            )
                        })}
                    </div>
                </div>

         </div>
         </div>
         
      </div>
   </div>
   )
}

export default A