import * as echarts from 'echarts';
import React, {useState,useEffect} from "react";


export default function Bing(){
   useEffect (() => {
      var chartDom = document.getElementById('bing1');
      var myChart = echarts.init(chartDom);
      myChart.setOption({ 
         tooltip: {
            trigger: 'item'
        },
        legend: {
            bottom:0
        },
        series: [
            {
                name: '访问来源',
                type: 'pie',
                radius: '50%',
                data: [
                  {value: 0, name: '2021-01'},
                  {value: 0, name: '2021-02'},
                  {value: 0, name: '2021-03'},
                  {value: 0, name: '2021-04'},
                  {value: 0, name: '2021-05'},
                  {value: 0, name: '2021-06'},
                  {value: 0, name: '2021-07'},
                  {value: 0, name: '2021-08'},
                  {value: 0, name: '2021-09'},
                  {value: 0, name: '2021-10'},
                  {value: 0, name: '2021-11'},
                  {value: 0, name: '2021-12'}
                ],
                emphasis: {
                    itemStyle: {
                        shadowBlur: 10,
                        shadowOffsetX: 0,
                        shadowColor: 'rgba(0, 0, 0, 0.5)'
                    }
                }
            }
        ]
      })
   })
   return(
      <div id="bing1"></div>
   )
}