import * as echarts from 'echarts';
import axios from 'axios'
import { Table } from 'antd'
import React, {useState,useEffect} from "react";
import '../router-view.css'

export default function Demo(){
   

  
   const [arr,setarr] = useState([])
   //人员名单
   axios.post('http://crm.cimns.com/index.php/bi/customer/userCycle').then((data)=>{
      if(arr.length<=0){
      let a = [...arr]
      a = [...data.data.data.users]
      setarr(a)
      }
   },(err)=>{
   console.log(err)
   })

    // 统计图
    useEffect (() => {
      
      var myChart = echarts.init(document.getElementById('main'));
      myChart.setOption({
       tooltip: {
          trigger: 'axis',
          axisPointer: {            // 坐标轴指示器，坐标轴触发有效
              type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
          }
      },
       color: ['#6ca2ff','#ff7474'],
       legend: {
         bottom:0
       },
      
    
       dataset: {
           source: [
               ['product', '成交客户数', '成交周期'],
               ['2021-01', 0,0],
               ['2021-02', 0,0],
               ['2021-03', 0,0],
               ['2021-04', 0,0],
               ['2021-05', 0,0],
               ['2021-06', 0,0],
               ['2021-07', 0,0],
               ['2021-08', 0,0],
               ['2021-09',0,0],
               ['2021-10', 0,0],
               ['2021-11', 0,0],
               ['2021-12', 0,0]
           ]
       },
       xAxis: {type: 'category'},
       yAxis: [
         {
            type: 'value',
            name: '成交周期',
            min: 0,
            max: 1,
            interval: 0.2,
            axisLabel: {
                formatter: '{value} 天'
            }
        },
        {
            type: 'value',
            name: '成交客户数',
            min: 0,
            max: 1,
            interval: 0.2,
            axisLabel: {
                formatter: '{value} 个'
            }
        },
            {
           type : 'value',
           axisLine:{show:true,
           lineStyle:{color:'#ccc'}
           },
           axisTick:{
           show:false,                          
           },
           axisLabel:{
           
           },
           splitNumber:5
            
                                 }
          ],
       
       series: [
           {type: 'bar'},
           {type: 'bar'},
       ]
    })
      })
      //统计图结束

     
         return(
            <div className="border_boxa">
              
      <div className="max_box">
      <div className="padin">
         <div className="max_select">
            <div className="item_select">
               <select key="a"  >
               <option key="volvo" >今天</option>
               <option key="saab" >昨天</option>
               <option key="opel" >本周</option>
               <option key="audi" >上周</option>
               <option key="opel1" >本月</option>
               <option key="audi1" >上月</option>
               <option key="opel3"  selected>今年</option>
               <option key="audi0" >去年</option>
               <option key="opel2" >自定义</option>
               </select>
            </div>
            <div className="item_select">
               <select key="b"  >
               <option key ="volvo" selected>办公室</option>
               <option key ="saab">打杂部</option>
               </select>
            </div>
            <div className="item_select">
               <select key="c" >
               <option key="" disabled selected hidden>选择员工</option>
               {arr.map((item,index)=>{
                  return(
                     <option key ={index}>{item.realname}</option>
                  )
               })}
               
               
               </select>
            </div>
               <button className="btn1">搜索</button>
            <div>
               <p style={{color:'#333',fontSize:'16px',marginTop:'17px',marginLeft:'101px'}}>员工客户成交周期（根据合同下单时间和客户创建时间计算）</p>
            </div>
         </div>
         <div id="main"></div>
         <div className="tabl">

         <div className="no3_1_1_box_content_list">
                    <div className="no3_1_1_box_content_list_top">
                        <div>姓名</div>
                        <div>成交周期（天）</div>
                        <div>成交客户数</div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent">
                        {arr.map((item, index) => {
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item.realname}</div>
                                    <div>{item.cycle}</div>
                                    <div>{item.customer_num}</div>
                                </div>
                            )
                        })}
                    </div>
                </div>
         </div>
         </div>
         
      </div>
   </div>
         )
}
