import './off1.css'
import React, { useState } from 'react';
import axios from 'axios'
import { Tabs, Button, Select, Input, DatePicker, Space, Modal } from 'antd';
import 'moment/locale/zh-cn';
import locale from 'antd/es/date-picker/locale/zh_CN';
const { TabPane } = Tabs;
const { Option } = Select;
function handleChange(value) {
    console.log(`selected ${value}`);
}
function callback(key) {
    console.log(key);
}
function Off1(params) {
    const [visible, setVisible] = useState(false)
    const { TextArea } = Input;
    const [lolp, setlolp] = useState([])
    axios.post('http://crm.cimns.com/index.php/admin/users/getUserList').then((data) => {
        if (data.data.code === 101) {
            console.log(data.data.arr)
        }
        else {
            if (lolp.length <= 0) {
                setlolp(data.data.data)
            }
        }
    }, (err) => {
        console.log(err)
    })
    function onChange1(date, dateString) {
        console.log(date, dateString);
    }
    function onChange2(date, dateString) {
        console.log(date, dateString);
    }
    return (
        <div className="off1">
            <div className="off1top">
                <Tabs defaultActiveKey="1" onChange={callback}>
                    <TabPane tab="公告" key="1">
                        <div className="off1bottom1">
                            <label className="offlabel1">公告状态</label>
                            <Select defaultValue="公示中" style={{ width: 180 }} onChange={handleChange} className="off1sel">
                                <Option value="jack">公示中</Option>
                                <Option value="Yiminghe">已结束</Option>
                            </Select>
                        </div>
                    </TabPane>
                </Tabs>
                <div className="offtoright1">
                    <Button type="primary" onClick={() => setVisible(true)} className="offtoribtn1">新建公告</Button>
                </div>
                {/* 弹窗 */}
                <div>
                    <Modal title="新建公告" centered
                        visible={visible}
                        okText="保存" cancelText="取消" width={700}>
                        <div style={{ width: 660, height: 392 ,marginLeft: 21, marginTop: 10}}>
                            <div className='offtoricon'>
                                <p className="offtoriconinfo">*公告标题</p>
                                <Input className="offtorinput" />
                            </div>

                            <div className="offtoricon1">
                                <p className="offtoriconinfo1">通知部门</p>
                                <Select onChange={handleChange} style={{ width: 280 }}>
                                    {lolp.map((item, idx) => {
                                        return (
                                            <div key={idx}>
                                                <p>{item.username}</p>
                                            </div>
                                        )
                                    })}
                                </Select>
                            </div>
                            <div className="offtoricon2">
                                <p className="offtoriconinfo2">开始时间</p>
                                <Space direction="vertical">
                                    <DatePicker onChange={onChange1} locale={locale} className="offtorida" />
                                </Space>
                            </div>
                            <div className="offtoricon3">
                                <p className="offtoriconinfo3">结束时间</p>
                                <Space direction="vertical">
                                    <DatePicker onChange={onChange2} locale={locale} className="offtorida" />
                                </Space>
                            </div>
                            <div className="off1toricon4">
                                <p className="offtoriconinfo5">公告正文</p>
                                <TextArea rows={4} />
                            </div>
                        </div>
                    </Modal>
                </div>
            </div>
        </div>
    )
}

export default Off1