import './man2_1.css'
import { HashRouter} from "react-router-dom"
import { Tooltip,Select,Modal, Button,Space,message,Table,Pagination} from 'antd';
import {
    QuestionCircleOutlined,
    CloseCircleOutlined,
    ExclamationCircleOutlined,
  } from '@ant-design/icons';
import React,{useState } from 'react'

const text = <span>下次跟进的时间为今日的客户</span>;
const provinceData = ['今日需联系', '已逾期','已联系'];
const provinceDat = ['我的','我下属的'];
const provinceDa = ['请选择要筛选的字段名','客户名称','客户级别','客户行业','客户来源','成交状态','电话','网址','下次联系时间','备注','手机','创建人','更新时间','创建时间','负责人','地区定位'];
const provinceD = ['等于','不等于','包含','不包含','开始于','结束于','为空','不为空','大于','大于等于','小于','小于等于'];
const provinceDaa = ['请选择筛选条件','IT/通信/电子/互联网','金融业','房地产','商业服务','贸易','生产','运输/物流','服务业','文化传媒','政府','其他'];
const handleProvinceChange = value => {};
const { Option } = Select;
const { confirm } = Modal;
const columns = [
    { title: '客户名称', dataIndex: 'address', key: '1' },
    { title: '客户级别', dataIndex: 'address', key: '2' },
    { title: '客户行业', dataIndex: 'address', key: '3' },
    { title: '客户来源', dataIndex: 'address', key: '4' },
    { title: '成交状态', dataIndex: 'address', key: '5' },
    { title: '电话', dataIndex: 'address', key: '6' },
    { title: '网址', dataIndex: 'address', key: '7' },
    { title: '下次联系时间', dataIndex: 'address', key: '8' },
    { title: '备注', dataIndex: 'address', key: '9' },
    { title: '手机', dataIndex: 'address', key: '10' },
    { title: '创建人', dataIndex: 'address', key: '11' },
    { title: '更新时间', dataIndex: 'address', key: '12' },
    { title: '创建时间', dataIndex: 'address', key: '13' },
    { title: '负责人', dataIndex: 'address', key: '14' },
    { title: '跟进级路', dataIndex: 'address', key: '15' },
    { title: '省，市，区/县', dataIndex: 'address', key: '16' },
    { title: '详细地址', dataIndex: 'address', key: '17' },
    { title: '', dataIndex: 'address', key: '18' },
  ];

function showConfirm() {
    confirm({
      title: '提示',
      icon: <ExclamationCircleOutlined />,
      content: '您确定要删除这一条数据吗？',
      onOk() {
        console.log('OK');
        message.success('已添加')
      },
      onCancel() {
        console.log('Cancel');
        message.warning('已取消删除');
      },
      okText:'确定',
      cancelText:'取消',

    });
  }
  function showTotal(total) {
    return `Total ${total} items`;
  }

let Man2_1 = function (params){
    const [visible, setVisible] = useState(false);
    return(
        <HashRouter>
            <div className="lxkh">
                    <div className="lxkh_title">
                        今日需联系客户
                        <Tooltip placement="top" title={text}>
                                <QuestionCircleOutlined style={{marginLeft:"10px",color:" gray"}}/>
                        </Tooltip>
                    </div>
                    <div className="option_bar">
                        <div className="option_data">
                            <Select  style={{width:"185px"}} defaultValue={provinceData[0]} onChange={handleProvinceChange}>
                                {provinceData.map(province => (
                                    <Option key={province}>{province}</Option>
                                ))}
                            </Select>
                            <Select  style={{width:"120px",margin:"0px 30px 0px 8px"}} defaultValue={provinceDat[0]} onChange={handleProvinceChange}>
                                {provinceDat.map(province => (
                                    <Option key={province}>{province}</Option>
                                ))}
                            </Select>
                            <Button type="primary" onClick={() => setVisible(true)} style={{background:"none",border:"none",color:"#000",boxShadow:"0 0 0 0"}}>
                                <img src="data:image/png;base64," style={{width:"12px",height:"13px",margin: "0 5px",verticalAlign:"middle"}}/>
                                高级筛选
                            </Button>
                            <Modal
                                title="高级筛选"
                                centered
                                visible={visible}
                                onOk={() => setVisible(false)}
                                onCancel={() => setVisible(false)}
                                width={900}
                                okText='确定'
                                cancelText='取消'
                            >
                               <div style={{marginBottom:"10px"}}>筛选条件</div>
                               <Select  style={{width:"286px",height:"40px",float:"left"}} defaultValue={provinceDa[0]} onChange={handleProvinceChange}>
                                    {provinceDa.map(province => (
                                        <Option key={province}>{province}</Option>
                                    ))}
                                </Select>
                               <div style={{width:"35px",float:"left"}}>&nbsp;</div>
                               <Select  style={{width:"143px",height:"40px",marginRight:"35px"}} defaultValue={provinceD[0]} onChange={handleProvinceChange}>
                                    {provinceD.map(province => (
                                        <Option key={province}>{province}</Option>
                                    ))}
                                </Select>
                                <Select  style={{width:"286px",height:"40px"}} defaultValue={provinceDaa[0]} onChange={handleProvinceChange}>
                                    {provinceDaa.map(province => (
                                        <Option key={province}>{province}</Option>
                                    ))}
                                </Select>
                                <Space>
                                    <Button onClick={showConfirm} style={{border:"none"}}>
                                        <CloseCircleOutlined />
                                    </Button>
                                </Space>
                                <p>+添加筛选条件</p>   
                            </Modal>
                        </div>
                    </div>
                    <div className="lxkh_table">
                        <Table columns={columns} scroll={{ x: 1918 }} />
                    </div>
                    <div className="lxkh_footer">
                        <Pagination size="small" total={50} showSizeChanger showQuickJumper />
                    </div>
            </div>
        </HashRouter>
    )
}  
export default Man2_1