import {Component} from 'react';
import { Layout, Menu,} from 'antd';
import {
  DesktopOutlined,
  CalendarOutlined,
  WalletOutlined,
  GatewayOutlined,
  ProfileOutlined,
  PrinterOutlined,
  LayoutOutlined
} from '@ant-design/icons';
import { HashRouter,Switch,Route,Link } from 'react-router-dom'
import './nav.css'
import './wrap.css'
import Creates from './creates'
import Bworks from '../view/bworks'
import Boffice from '../view/boffice'
import Off from '../view/off'
import Off1 from '../view/off1'
import Off2 from '../view/off2'
import Off3 from '../view/off3'
import Off4 from '../view/off4'

const {  Content,  Sider } = Layout;

class No1 extends Component {
  state = {
    collapsed: false,
    type:true
  };

  onCollapse = collapsed => {
    this.setState({ collapsed });
    console.log(collapsed);
    this.setState({type:!this.state.type})
    console.log(this.state.type);
  };

  render() {
    const { collapsed } = this.state;
    return (
     
      <div className='no1'>
         <HashRouter>
          <Layout style={{ minHeight: '100vh',paddingTop:'70px' }}>
            <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse}>
              <Creates showType={this.state.type}/>
              <div className="logo" />
              <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
                <Menu.Item key="1" icon={<DesktopOutlined />}>
                  <Link to="/app">工作台</Link>
                </Menu.Item>
                <Menu.Item key="2" icon={<LayoutOutlined />}>
                  <Link to="/app/boffice">日程</Link>
                </Menu.Item>
                <Menu.Item key="3" icon={<PrinterOutlined />}>
                  <Link to="/app/Off">任务</Link>
                </Menu.Item>
                <Menu.Item  key="4" icon={<CalendarOutlined />}>
                  <Link to="/app/Off1">公告</Link>
                </Menu.Item>
                <Menu.Item key="5" icon={<WalletOutlined />}>
                  <Link to="/app/Off2">日志</Link>
                </Menu.Item>
                <Menu.Item key="6" icon={<GatewayOutlined />}>
                  <Link to="/app/Off3">审批</Link>
                </Menu.Item>
                <Menu.Item key="7" icon={<ProfileOutlined />}>
                  <Link to="/app/Off4">通讯录</Link>
                </Menu.Item>
               
              </Menu>
            </Sider>
            <Layout className="site-layout">
              <Content style={{ margin: '0 16px' }}>
              {/* minHeight: 360  */}
                <div className="site-layout-background" style={{ padding:'24px 0px',}}>
                  <Switch>
                    <Route path="/app" component={Bworks} exact/>
                    <Route path="/app/boffice" component={Boffice} />
                    <Route path="/app/Off" component={Off} />
                    <Route path="/app/Off1" component={Off1} />
                    <Route path="/app/Off2" component={Off2} /> 
                    <Route path="/app/Off3" component={Off3} /> 
                    <Route path="/app/Off4" component={Off4} /> 
                  </Switch>
                </div>
              </Content>
            </Layout>
          </Layout>
        </HashRouter>
        </div>
    );
  }
}

export default No1
