import './off.css'
import React, { useState } from 'react';
import axios from 'axios'
import { Button, Select, Modal, Input, Space, DatePicker, Tabs } from 'antd';
import 'moment/locale/zh-cn';
import locale from 'antd/es/date-picker/locale/zh_CN';
// import { DownOutlined } from '@ant-design/icons';
const { Option } = Select;
const { TabPane } = Tabs;
function handleChange(value) {
    console.log(`selected ${value}`);
}

function callback(key) {
    console.log(key);
}

function Off(props) {
    const [arr1, setarr1] = useState(['全部', '我负责的', '我创建的', '我参与的',]);
    const [arr2, setarr2] = useState(['全部', '正在进行', '已完成',]);
    const [arr3, setarr3] = useState(['全部', '高', '中', '低', '无']);
    const [visible, setVisible] = useState(false)
    const [state, setState] = useState('')
    const { TextArea } = Input;
    const hideModal = () => {
        setState({
            visible: false,
        });
    };
    const [arr, setarr] = useState([{
        text: '高',
        style: {
            border: "1px solid #ed6363",
            color: "#ed6363"
        }
    }, {
        text: '中',
        style: {
            border: "1px solid #ff9668",
            color: "#ff9668"
        }
    }, {
        text: '低',
        style: {
            border: "1px solid #8bb5f0",
            color: "#8bb5f0"
        }
    }, {
        text: '无',
        style: {
            border: "1px solid #ccc",
            color: "#ccc"
        }
    }])
    function getindex(i) {
        setindex(i)
    }
    const [lolp, setlolp] = useState([])
    axios.post('http://crm.cimns.com/index.php/admin/users/getUserList').then((data) => {
        if (data.data.code === 101) {
            console.log(data.data.arr)
        }
        else {
            if (lolp.length <= 0) {
                setlolp(data.data.data)
            }
        }
    }, (err) => {
        console.log(err)
    })
    const [name, setname] = useState('')
    let [val, setval] = useState('')
    const [time, settime] = useState('');
    const [time2, settime2] = useState('');
    const [index, setindex] = useState(arr.lenght - 1)
    let fromjson = {
        main_user_id: name,
        start_time: time,
        stop_time: time2,
        priority: 0,
        name: val,
        is_top: props.createId
    }

    function onChange1(date, dateString) {
        console.log(date, dateString);
    }
    function onChange2(date, dateString) {
        console.log(date, dateString);
    }


    function hide() {
        setVisible(false)
    }
    function handleVisibleChange(visible) {
        setVisible(visible)
    }
    return (
        <div className="off">
            <div className="offtop">
                <div>
                    <Tabs defaultActiveKey="1" onChange={callback}>
                        <TabPane tab="我的任务" key="1">
                            <div className="offright">
                                <div className="offrinput">
                                    <input type="text" autocomplete="off" placeholder="搜索任务名称" class="offin" />
                                    <div className="offribtn">
                                        <div className="offribtncon">
                                            <label className="offlabel">任务类型</label>
                                            <div className="offribco">
                                                <Select defaultValue="全部" onChange={handleChange} style={{ width: 120 }} className="ant">
                                                    {arr1.map((item, idx) => {
                                                        return (
                                                            <div key={idx}>
                                                                <p>{item}</p>
                                                            </div>
                                                        )
                                                    })}
                                                </Select>
                                            </div>
                                        </div>
                                        <div className="offribtncon1">
                                            <label className="offlabel">状态</label>
                                            <div className="offribco1">
                                                <Select defaultValue="全部" onChange={handleChange} style={{ width: 120 }} className="ant">
                                                    {arr2.map((item, idx) => {
                                                        return (
                                                            <div key={idx}>
                                                                <p>{item}</p>
                                                            </div>
                                                        )
                                                    })}
                                                </Select>
                                            </div>
                                        </div>
                                        <div className="offribtncon2">
                                            <label className="offlabel2">优先级</label>
                                            <div className="offribco2">
                                                <Select defaultValue="全部" onChange={handleChange} style={{ width: 120 }} className="ant">
                                                    {arr3.map((item, idx) => {
                                                        return (
                                                            <div key={idx}>
                                                                <p>{item}</p>
                                                            </div>
                                                        )
                                                    })}
                                                </Select>
                                            </div>
                                        </div>
                                        <div className="offribtncon3">
                                            <label className="offlabel3">截止时间</label>
                                            <Select defaultValue="全部" onChange={handleChange} style={{ width: 119 }} className="ant">
                                                {arr1.map((item, idx) => {
                                                    return (
                                                        <div key={idx}>
                                                            <p>{item}</p>
                                                        </div>
                                                    )
                                                })}
                                            </Select>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </TabPane>
                        <TabPane tab="我下属的任务" key="2">
                            <div className="offright">
                                <div className="offrinput">
                                    <input type="text" autocomplete="off" placeholder="搜索任务名称" class="offin" />
                                    <div className="offribtn">
                                        <div className="offribtncon">
                                            <label className="offlabel">任务类型</label>
                                            <div className="offribco">
                                                <Select defaultValue="全部" onChange={handleChange} style={{ width: 120 }} className="ant">
                                                    {arr1.map((item, idx) => {
                                                        return (
                                                            <div key={idx}>
                                                                <p>{item}</p>
                                                            </div>
                                                        )
                                                    })}
                                                </Select>
                                            </div>
                                        </div>
                                        <div className="offribtncon1">
                                            <label className="offlabel">状态</label>
                                            <div className="offribco1">
                                                <Select defaultValue="全部" onChange={handleChange} style={{ width: 120 }} className="ant">
                                                    {arr2.map((item, idx) => {
                                                        return (
                                                            <div key={idx}>
                                                                <p>{item}</p>
                                                            </div>
                                                        )
                                                    })}
                                                </Select>
                                            </div>
                                        </div>
                                        <div className="offribtncon2">
                                            <label className="offlabel2">优先级</label>
                                            <div className="offribco2">
                                                <Select defaultValue="全部" onChange={handleChange} style={{ width: 120 }} className="ant">
                                                    {arr3.map((item, idx) => {
                                                        return (
                                                            <div key={idx}>
                                                                <p>{item}</p>
                                                            </div>
                                                        )
                                                    })}
                                                </Select>
                                            </div>
                                        </div>
                                        <div className="offribtncon3">
                                            <label className="offlabel3">截止时间</label>
                                            <Select defaultValue="全部" style={{ width: 119 }} onChange={handleChange} className="offrisel">
                                                <Option>全部</Option>
                                                <Option>我负责的</Option>
                                                <Option>我创建的</Option>
                                                <Option>我参与的</Option>
                                            </Select>
                                        </div>
                                        <div className="offribtncon4">
                                            <label className="offlabel4">负责人</label>
                                            <Select onChange={handleChange} style={{ width: 119, margin: '10px 0 ' }} defaultValue="全部">
                                                {lolp.map((item, idx) => {
                                                    return (
                                                        <Option key={item.username + item.id}>{item.username + item.id}</Option>
                                                    )
                                                })}
                                            </Select>
                                        </div>
                                    </div>
                                </div>

                            </div>
                        </TabPane>
                    </Tabs>
                </div>
                <div className="offtoright">
                    <Button type="primary" onClick={() => setVisible(true)} className="offtoribtn">新建任务</Button>
                </div>
                {/* 弹窗 */}
                <div style={{ margin: '0 460px' }}>
                    <Modal title="新建任务" centered
                        visible={visible}
                        okText="保存" cancelText="取消" width={700}>
                        <div style={{ width: 660, height: 450, marginLeft: 21, marginTop: 10 }}>
                            <div className='offtoricon'>
                                <p className="offtoriconinfo">*任务名称</p>
                                <Input className="offtorinput" />
                            </div>

                            <div className="offtoricon1">
                                <p className="offtoriconinfo1">负责人</p>
                                <Select onChange={handleChange} style={{ width: 280 }}>
                                    {lolp.map((item, idx) => {
                                        return (
                                            <div key={idx}>
                                                <p>{item.username}</p>
                                            </div>
                                        )
                                    })}
                                </Select>
                            </div>
                            <div className="offtoricon2">
                                <p className="offtoriconinfo2">开始时间</p>
                                <Space direction="vertical">
                                    <DatePicker onChange={onChange1} locale={locale} className="offtorida" />
                                </Space>
                            </div>
                            <div className="offtoricon3">
                                <p className="offtoriconinfo3">结束时间</p>
                                <Space direction="vertical">
                                    <DatePicker onChange={onChange2} locale={locale} className="offtorida" />
                                </Space>
                            </div>
                            <div className="offtocon-level">
                                <p className="offtoriconinfo4">优先级</p>
                                <ul>
                                    {arr.map((item, idx) => {
                                        return (
                                            <li key={idx} onClick={() => { getindex(idx) }} style={item.style} >{item.text}</li>
                                        )
                                    })}
                                </ul>

                            </div>
                            <div className="offtoricon4">
                                <p className="offtoriconinfo5">任务描述</p>
                                <TextArea rows={4} style={{resize:'none'}}/>
                            </div>
                            <div >
                                <a style={{float:'left' }}>
                                    <img src="data:image/png;base64," style={{
                                        width: '16px',
                                        verticalAlign: 'middle'
                                    }} />
                                    关联业务
                                </a>
                            </div>
                        </div>
                    </Modal>
                </div>
            </div>
        </div>
    )
}


export default Off