
import { Tabs } from 'antd'
import React from "react";
import './router-view.css'
import YuanG from './s-03-component/yuangong'
import KeH from './s-03-component/kehu'
import ChanP from './s-03-component/chanpin'

let a = function () {
   const { TabPane } = Tabs;

   function callback(key) {
      console.log(key);
   }


   return (
      <div className="border_box">
         <div className="max_box">
            <div className="padin">
               <Tabs defaultActiveKey="1" onChange={callback}>
                  <TabPane tab="员工客户成交周期 " key="1">
                     <YuanG></YuanG>
                  </TabPane>
                  <TabPane tab="地区成交周期" key="2">
                     <KeH></KeH>
                  </TabPane>
                  <TabPane tab="产品成交周期" key="3">
                     <ChanP></ChanP>
                  </TabPane>
               </Tabs>
            </div>
         </div>
      </div>

   )
}

export default a

