import * as echarts from 'echarts';
import axios from 'axios'
import { Table } from 'antd'
import React, {useState,useEffect} from "react";
import './router-view.css'
export default function Lou(){
   const [arr,setarr] = useState([])
   const [arr1,setarr1] = useState([])
   //人员名单
   axios.post('http://crm.cimns.com/index.php/bi/customer/statistics').then((data)=>{
       if(arr.length<=0){
         let a = [...arr]
         a = [...data.data.data]
         setarr(a)
         console.log(a);
       }
    },(err)=>{
      console.log(err)
    })

    axios.post('http://crm.cimns.com/index.php/bi/business/funnel').then((data)=>{
      if(arr1.length<=0){
        let a = [...arr1]
        a = [...data.data.data.list]
        setarr1(a)
      }
   },(err)=>{
     console.log(err)
   })
    
   //  统计图
   useEffect (() => {


      
      var myChart = echarts.init(document.getElementById('main07'));
      myChart.setOption({
   
  tooltip: {
      trigger: 'item',
      formatter: '预测金额 : {c}元<br/>{b} ',
      backgroundColor : '#6e6e6e',
      borderColor : '#fff',
      textStyle:{
         color :'#fff'
      }
  },
  toolbox: {
      show: true,
      orient: 'vertical',
      top: 'center',
      feature: {
          saveAsImage: {}
      }
  },
  

  series: [
      {
          name: '客户验证（0）',
          type: 'funnel',
          width: '0%',
          height: '15%',
          left: '40%',
          top: '20%',
          funnelAlign: 'right',

          center: ['25%', '25%'],  // for pie

          data: [
              {value: 0, name: '客户验证（0）'},
              
          ]
      },
      {
         name: '需求分析（0）',
         type: 'funnel',
         width: '0%',
         height: '50%',
         left: '40%',
         top: '20%',
         funnelAlign: 'right',

         center: ['25%', '25%'],  // for pie

         data: [
             {value: 0, name: '需求分析（0）'},
             
         ]
     },
     {
      name: '方案/报价（0）',
      type: 'funnel',
      width: '0%',
      height: '85%',
      left: '40%',
      top: '20%',
      funnelAlign: 'right',

      center: ['25%', '25%'],  // for pie

      data: [
          {value: 0, name: '方案/报价（0）'},
          
      ]
  },
  {
   name: '谈判审核（0）',
   type: 'funnel',
   width: '0%',
   height: '120%',
   left: '40%',
   top: '20%',
   funnelAlign: 'right',

   center: ['25%', '25%'],  // for pie

   data: [
       {value: 0, name: '谈判审核（0）'},
       
   ]
},
     
  ]
})
})
//   统计图结束

       return(
         <div className="border_box">
              
         <div className="max_box">
         <div className="padin">
            <div className="max_select">
               <div className="item_select">
                  <select key="a" >
                  <option key ="volvo" >今天</option>
                        <option key ="saab">昨天</option>
                        <option key="opel">本周</option>
                        <option key="audi">上周</option>
                        <option key="opel1">本月</option>
                        <option key="audi1">上月</option>
                        <option key="opel2" selected>今年</option>
                        <option key="audi2">去年</option>
                        <option key="opel3">自定义</option>
                  </select>
               </div>
               <div className="item_select">
                  <select key="b">
                  <option key ="办公室" selected>办公室</option>
                  <option key ="打杂部">打杂部</option>
                  </select>
               </div>
               <div className="item_select">
                  <select key="c" defaultValue="">
                  <option key="5" disabled selected hidden>选择员工</option>
                  {arr.map((item,index)=>{
                     return(
                        <option key ={index}>{item.realname}</option>
                     )
                  })}
                  </select>
               </div>
               <div className="item_select">
                  <select key="c" defaultValue="" >
                  <option key="5dsa" disabled selected hidden>系统默认</option>
                  </select>
               </div>
                  <button className="btn1">搜索</button>
               
            </div>
            <div className="mains" id="main07">
               
            </div>
            <div className="tabl">
   
            <div className="no3_1_1_box_content_list">
                    <div className="no3_1_1_box_content_list_top">
                        <div>阶段</div>
                        <div>金额</div>
                        <div>商机数</div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent">
                        {arr1.map((item, index) => {
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item.name}</div>
                                    <div>{item.money}</div>
                                    <div>{item.count}</div>
                                </div>
                            )
                        })}
                    </div>
                </div>
            </div>
            </div>
            
         </div>
      </div>
       )
}

