import './off4.css'
import { useState } from 'react'
import axios from 'axios';
import { Tabs, Collapse, } from 'antd';
const { TabPane } = Tabs;
const { Panel } = Collapse;
// const onSearch = value => console.log(value);
function callback(key) {
    console.log(key);
}
function Off4() {
    const [arr, setarr] = useState({})
    const [type, settype] = useState([])
    const [arl, setarl] = useState([])
    const [list, setlist] = useState([])

    axios.post('http://crm.cimns.com/index.php/oa/addresslist/index', {
        type: 1
    })
        .then(data => {
            if (type) {
                setarr(data.data.data)
                settype(false)
                conversion(data.data.data)
            }
        })
        .catch(err => {
            console.error(err);
        })
    axios.post('http://crm.cimns.com/index.php/oa/addresslist/index')
        .then(data => {
            if (list.length <= 0) {
                setlist(data.data.data)
            }
        })
        .catch(err => {
            console.error(err);
        })
    function conversion(data) {
        let arr1 = []
        for (const key in data) {
            arr1.push(key)
        }
        setarl(arr1)
    }
    return (
        <div className="off4">
            <div className="off4top">
                <Tabs defaultActiveKey="1" onChange={callback}>
                    <TabPane tab="员工" key="1">
                        <div className="off4bottom">
                            <div className="off4boser">
                                <input type="text" autocomplete="off" placeholder="搜索成员" class="off4in" />
                            </div>
                        </div>
                        <div className='off4bobottom'>
                            <div className="off7-cont">
                                <ul className="off7-com">
                                    {arl.map((item, index) => {
                                        return (
                                            <li className="off7-item" key={index}>
                                                <p className="of7-title">{item}</p>
                                                {arr[item].map((itm, inde) => {
                                                    return (
                                                        <div className="itsl" key={inde}>
                                                            <p className="name">{itm.realname}</p>
                                                            <div className="xit">
                                                                <p><i className="iconfont">&#xe705;</i> {itm.structure_name}</p>
                                                                <p style={{ display: itm.post ? 'block' : 'none' }}><i className="iconfont">&#xe617;</i>{itm.post}</p>
                                                                <p><i className="iconfont">&#xe621;</i>{itm.username}</p>
                                                            </div>
                                                        </div>
                                                    )
                                                })}
                                            </li>
                                        )
                                    })}
                                </ul>
                            </div>
                        </div>
                    </TabPane>
                    <TabPane tab="部门" key="2">
                        <div className="off4bottom">
                            <div className="off4boser">
                                <input type="text" autocomplete="off" placeholder="搜索成员" class="off4in" />
                            </div>
                            <div className='off4bobottom'>
                                <Collapse defaultActiveKey={['1']} ghost>
                                    {list.map((item, index) => {
                                        return (
                                            <Panel header={item.name + '(' + item.userList.length + ')'} key={index}>
                                                <div className="off7-cont">
                                                    <ul className="off7-com">
                                                        {item.userList.map((itm, inde) => {
                                                            return (
                                                                <li className="off7-item" key={inde}>
                                                                    <div className="itsl" key={inde}>
                                                                        <p className="name">{itm.realname}</p>
                                                                        <div className="xit">
                                                                            <p><i className="iconfont">&#xe705;</i> {itm.structure_name}</p>
                                                                            <p style={{ display: itm.post ? 'block' : 'none' }}><i className="iconfont">&#xe617;</i>{itm.post}</p>
                                                                            <p><i className="iconfont">&#xe621;</i>{itm.username}</p>
                                                                        </div>
                                                                    </div>
                                                                </li>
                                                            )
                                                        })}
                                                    </ul>
                                                </div>
                                            </Panel>
                                        )
                                    })}
                                </Collapse>
                            </div>
                        </div>
                    </TabPane>
                </Tabs>
            </div>
        </div >
    )
}
export default Off4