import './demo1-create.css'
import axios from 'axios' //数据
import {useState,useEffect} from 'react'
import { Input, DatePicker, Space, Popover, Button , Select } from 'antd';
import 'moment/locale/zh-cn';
import locale from 'antd/es/date-picker/locale/zh_CN';
import {
    CloseOutlined,
} from '@ant-design/icons';
  


let Demo1Create = function(props){
    // console.log(props);
    const { TextArea } = Input;
    const [visible,setvisible] = useState(false)
    const { Option } = Select;
    const [arr,setarr] = useState([{
        text:'高',
        style:{
            border: "1px solid #ed6363",
            color: "#ed6363"
        }
    },{
        text:'中',
        style:{
            border: "1px solid #ff9668",
            color: "#ff9668"
        }
    },{
        text:'低',
        style:{
            border: "1px solid #8bb5f0",
            color: "#8bb5f0"
        }
    },{
        text:'无',
        style:{
            border: "1px solid #ccc",
            color: "#ccc"
        }
    }])
    
    function getindex(i){
    //   console.log(i);
        setindex(i)
    }
    function hide(){
        setvisible(false)
    }
    function handleVisibleChange (visible){
        setvisible(visible)
    }
    const [lolp, setlolp] = useState([])
    
    axios.post('http://crm.cimns.com/index.php/admin/users/getUserList').then((data) => {
        if (data.data.code === 101) {
            console.log(data.data.arr)
        }
        else{
            if(lolp.length <=0){
            //   console.log(data.data.data);
                setlolp(data.data.data)
            }
        }
    //   console.log(lolp)
    }, (err) => {
        console.log(err)
    })
    function handleChange(value) {
        // console.log(value);
        setname(value)
    }
    const [name, setname] = useState('')
    let [val,setval] = useState('')
    const [time,settime] = useState('');
    const [time2,settime2] = useState('');
    const [index,setindex] = useState(arr.lenght-1)
    let fromjson ={
        main_user_id: name,
        start_time: time,
        stop_time: time2,
        priority: 0,
        name: val,
        is_top: props.createId
    }
    function onChange1(date, dateString) {
        // console.log(date, dateString);
        settime(dateString)
    }
    function onChange2(date, dateString2) {
        // console.log(date, dateString2);
        settime2(dateString2)
    }
    function valChange(event){
        setval(event.target.value)
    }
   
    return(
        <div className="demo1create">
            <div className="title-box">
                <p className="hd">新建任务</p>
            </div>
            <div className="top">
                <div className="create-left">
                    <div className="input-box">
                        <div className="input-info">
                            <p className="info">任务名称</p>
                            <Input placeholder="" onChange={valChange} />
                        </div>
                        <div className="input-info">
                            <p className="info">开始时间</p>
                            <Space direction="vertical">
                                <DatePicker onChange={onChange1} locale={locale} />
                            </Space>
                        </div>
                    </div>
                </div>
                <div className="create-right">
                    <div className="input-box">
                    <div className="input-info">
                            <p className="info">负责人</p>
                            <Select onChange={handleChange} style={{ width: '100%' }}>
                                {lolp.map((item, idx) => {
                                    return (
                                        // <option value={item.username + item.id} title={item.username} key={item.username + item.id}></option>
                                        <Option key={item.username + item.id}>{item.username + item.id}</Option>
                                        // <div key={idx}>
                                        //     <p>{item.username}</p>
                                        // </div>
                                    )
                                })}
                            </Select>
                        </div>
                        <div className="input-info">
                            <p className="info">结束时间</p>
                            <Space direction="vertical">
                                <DatePicker onChange={onChange2} locale={locale} />
                            </Space>
                        </div>
                    </div>
                </div>
            </div>
            <div className="demo1-level">
                <p className="info">优先级</p>
                <ul>
                    {arr.map((item,idx)=>{
                        return(
                        <li key={idx} onClick={()=>{getindex(idx)}} style={item.style} >{item.text}</li>
                        )
                    })}
                </ul>
            </div>
            <div>
                <p className="info">任务描述</p>
                <TextArea rows={4} />
            </div>
            <div>
                <Popover
                    content={<a onClick={hide}>Close</a>}
                    title="Title"
                    trigger="click"
                    visible={visible}
                    onVisibleChange={handleVisibleChange}
                >
                    <Button type="primary">Click me</Button>
                </Popover>
            </div>
        </div>
    )
}

export default Demo1Create;