import './man2.css'
import { HashRouter, Link, Route,Switch} from "react-router-dom"
import {useState} from 'react'
import Man2_1 from '../view/man2_1'
import Man2_2 from '../view/man2_2'
import Man2_3 from '../view/man2_3'
import Man2_4 from '../view/man2_4'
import Man2_5 from '../view/man2_5'
import Man2_6 from '../view/man2_6'
import Man2_7 from '../view/man2_7'

let Man2 = function (params){
    const [arrnav,setarrnav] = useState([
        {
            path:'/app/manage/man2',
            title:'今日需联系客户'
        },{
            path:'/app/manage/man2/man2_2',
            title:'分配给我的线索'
        },{
            path:'/app/manage/man2/man2_3',
            title:'分配给我的客户'
        },{
            path:'/app/manage/man2/man2_4',
            title:'待审核合同'
        },{
            path:'/app/manage/man2/man2_5',
            title:'待审核回款'
        },{
            path:'/app/manage/man2/man2_6',
            title:'待回款提醒'
        },{
            path:'/app/manage/man2/man2_7',
            title:'即将到期的合同'
        }
    ])
    const [index,setindex] = useState(0)
    function getindedx(idx){
        setindex(idx)
        
    }
    console.log(index);
    
    return(
            <div>
                <div className="man2_header">待办事项</div>
                <div className="man2_body">
                    <div className="man2_body_side">
                        {arrnav.map((item,idx)=>{
                            return(
                                <div className={idx===index?"side_select act":"side_select"} onClick={()=>{
                                    getindedx(idx)
                                }}>
                                    <Link to={item.path}>{item.title}</Link>
                                </div>
                            )
                        })}
                    </div>
                    <div className="man2_body_content">
                        <Switch>
                            <Route path="/app/manage/man2" component={Man2_1} exact/>
                            <Route path="/app/manage/man2/man2_2" component={Man2_2}/>
                            <Route path="/app/manage/man2/man2_3" component={Man2_3}/>
                            <Route path="/app/manage/man2/man2_4" component={Man2_4}/>
                            <Route path="/app/manage/man2/man2_5" component={Man2_5}/>
                            <Route path="/app/manage/man2/man2_6" component={Man2_6}/>
                            <Route path="/app/manage/man2/man2_7" component={Man2_7}/>
                        </Switch>
                    </div>
                </div>
               
            </div>
    )
}
export default Man2