import './demo1.css'
import {useEffect,useState} from 'react'
import axios from 'axios'
import { Modal, Button, Space } from 'antd';
import Demo1Create from './demo1-create'
import {
    PlusOutlined,
  } from '@ant-design/icons';
let Demo1 = function(props){
    const [arr,setarr] = useState([])
    const [createId,setcreateId] = useState(0)
    const [json,setjson] = useState([]);
    const [jsonarr,setjosnarr] = useState([])
    useEffect(()=>{
        return(
            axios.post('http://crm.cimns.com/index.php/work/task/myTask').then((data)=>{
            if(data.data.code === 200){
                let newarr = [...arr]
                newarr = [...data.data.data]
                setarr(newarr)
            }
            },(err)=>{console.log(err);})
        )
    },[])
    // console.log(arr);
    function confirm() {
            Modal.confirm({
            content: <Demo1Create createId={createId} json={json} setjson={setjson}></Demo1Create>,
            okText: '确认',
            cancelText: '取消',
            closable:true,
            onOk() {
                let newjsonarr = [...jsonarr]
                newjsonarr.push({...json})
                // setjosnarr(newjsonarr)
                console.log(newjsonarr);
            },
            onCancel() {
                console.log('Cancel');
            },
        });
    }
    function showConfirm() {
        confirm({});
    }
    return (
        <div className="demo1"> 
            <p className="title">我的任务</p>
            <div className="demo1-position">
                <div className="demo1-cont">
                    {arr.map((item,idx)=>{
                        return(  
                            <div className="item" key={item.is_top}>
                                <div className="info-title">
                                    <p className="demo1-hd">{item.title}</p>
                                    <p>0 / 0</p>
                                </div>
                                <div className="demo1-line">
                                    <div className="line"></div>
                                </div>
                                <div className="demo1-cont-list">
                                    
                                </div>
                                <div className="newcreate" onClick={()=>{
                                    setcreateId(item.is_top)
                                }}>
                                    <PlusOutlined />
                                    <p onClick={showConfirm} >新建任务</p>
                                </div>
                            </div>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}
export default Demo1