import './demo1-create.css'
import axios from 'axios' //数据
import { useState, useEffect } from 'react'
import { Input, DatePicker, Space, Popover, Modal, Select } from 'antd';
import 'moment/locale/zh-cn';
import locale from 'antd/es/date-picker/locale/zh_CN';
import {
    PlusOutlined,
} from '@ant-design/icons';



let Bofficer = function (props) {
    const { TextArea } = Input;
    const [visible, setvisible] = useState(false)
    const { Option } = Select;
    const [addarr, setaddarr] = useState([])
    // const text = <Demo6Transfer tranArr={arr} addarr={addarr} setaddarr={setaddarr}></Demo6Transfer>;
    const [arr, setarr] = useState([{
        text: '高',
        style: {
            border: "1px solid #ed6363",
            color: "#ed6363"
        }
    }, {
        text: '中',
        style: {
            border: "1px solid #ff9668",
            color: "#ff9668"
        }
    }, {
        text: '低',
        style: {
            border: "1px solid #8bb5f0",
            color: "#8bb5f0"
        }
    }, {
        text: '无',
        style: {
            border: "1px solid #ccc",
            color: "#ccc"
        }
    }])

    function getindex(i) {
        //   console.log(i);
        setindex(i)
    }
    function hide() {
        setvisible(false)
    }
    function handleVisibleChange(visible) {
        setvisible(visible)
    }
    const [lolp, setlolp] = useState([])

    axios.post('http://crm.cimns.com/index.php/admin/users/getUserList').then((data) => {
        if (data.data.code === 101) {
            console.log(data.data.arr)
        }
        else {
            if (lolp.length <= 0) {
                setlolp(data.data.data)
            }
        }
    }, (err) => {
        console.log(err)
    })
    function handleChange(value) {
        setname(value)
    }
    const [name, setname] = useState('')
    let [val, setval] = useState('')
    const [time, settime] = useState('');
    const [time2, settime2] = useState('');
    const [index, setindex] = useState(arr.lenght - 1)
    let fromjson = {
        main_user_id: name,
        start_time: time,
        stop_time: time2,
        priority: 0,
        name: val,
        is_top: props.createId
    }
    function onChange1(date, dateString) {
        settime(dateString)
    }
    function onChange2(date, dateString2) {
        settime2(dateString2)
    }
    function valChange(event) {
        setval(event.target.value)
    }
    const [color, setcolor] = useState([{
        backgroundColor: 'rgb(83, 211, 151)'
    }, {
        backgroundColor: 'rgb(32, 193, 189)'
    }, {
        backgroundColor: 'rgb(88, 218, 218)'
    }, {
        backgroundColor: 'rgb(15, 201, 231)'
    }, {
        backgroundColor: 'rgb(52, 152, 219)'
    }, {
        backgroundColor: 'rgb(69, 134, 255)'
    }, {
        backgroundColor: 'rgb(137, 131, 243)'
    }, {
        backgroundColor: 'rgb(174, 161, 234)'
    }, {
        backgroundColor: 'rgb(255, 102, 153)'
    }, {
        backgroundColor: 'rgb(242, 77, 112)'
    }, {
        backgroundColor: 'rgb(255, 111, 111)'
    }])
    return (
        <div className="createtantext1 createtantext1-1">
            <div style={{ width: 630, height: 510, marginLeft: 21, marginTop: '20px' }}>
                <div className="demo6-name" style={{ marginLeft: '5px', marginTop: '2px' }}>
                    <p className="hd" style={{ paddingLeft: 0, borderLeft: 'none' }}> <span style={{ color: 'red', marginRight: 5 }}>*</span>主题</p>
                    <div className="ipt-box">
                        <Input placeholder="请输入内容" style={{ marginBottom: '5px' }} />
                        <span style={color[index]}></span>
                    </div>
                    <div className='color' style={{ marginTop: '3px' }}>
                        {color.map((item, idx) => {
                            return (
                                <span style={item} onClick={() => { getindex(idx) }} key={idx}></span>
                            )
                        })}
                    </div>
                </div>
                <div className="offtoricon2" style={{ marginRight: '45px' }}>
                    <p className="offtoriconinfo2"><span style={{ color: 'red', marginRight: 5 }}>*</span>开始时间</p>
                    <Space direction="vertical">
                        <DatePicker onChange={onChange1} locale={locale} className="offtorida" />
                    </Space>
                </div>
                <div className="offtoricon3" style={{ float: 'none' }}>
                    <p className="offtoriconinfo3"><span style={{ color: 'red', marginRight: 5 }}>*</span>结束时间</p>
                    <Space direction="vertical">
                        <DatePicker onChange={onChange2} locale={locale} className="offtorida" />
                    </Space>
                </div>
                <div style={{ marginTop: '15px', position: 'relative' }}>
                    <p className="offtoriconinfo3" style={{ marginLeft: '5px' }}>参与人</p>
                    <Input style={{ marginBottom: '5px', width: 280 }} /><PlusOutlined style={{ position: 'absolute', left: '249px', top: '37px' }} />
                    <div className="list-box">
                        <ul className="demo6-list">
                            {addarr.map((item, idx) => {
                                return (
                                    <li key={item.id} className="userImg">
                                        <img style={{ display: item.thumb_img === '' ? 'none' : 'block' }} src={item.thumb_img} alt=""></img>
                                        <span style={{ display: item.thumb_img === '' ? 'block' : 'none' }}>{item.realname}</span>
                                    </li>
                                )
                            })}
                        </ul>
                    </div>
                </div>
                <div className='off3man3-5' style={{ marginLeft: '0px' }}>
                    <p className="off3man3-5-title">备注</p>
                    <TextArea style={{ width: '611px', height: '160px' }} placeholder='请输入内容' />
                </div>
                <div>
                    <a style={{ float: 'left', marginTop: '15px' }}>
                        <img src="data:image/png;base64," style={{
                            width: '16px',
                            verticalAlign: 'middle'
                        }} />
                                    关联业务
                            </a>
                </div>
            </div>
        </div>
    )
}

export default Bofficer;