import * as echarts from 'echarts';
import React, {
   useState,
   useEffect
} from "react";


export default function Zhu() {
   useEffect(() => {
      var myChart = echarts.init(document.getElementById('zhu1'));
      myChart.setOption({
         tooltip: {
            trigger: 'axis',
            axisPointer: { // 坐标轴指示器，坐标轴触发有效
               type: 'shadow' // 默认为直线，可选为：'line' | 'shadow'
            }
         },
         color: ['#6ca2ff', '#ff7474'],
         legend: {
            bottom: 0
         },

         dataset: {
            source: [
               // ['product', '客户成交数', '新增客户数'],
               ['2021-01', 0, 0],
               ['2021-02', 0, 0],
               ['2021-03', 0, 0],
               ['2021-04', 0, 0],
               ['2021-05', 0, 0],
               ['2021-06', 0, 0],
               ['2021-07', 0, 0],
               ['2021-08', 0, 0],
               ['2021-09', 0, 0],
               ['2021-10', 0, 0],
               ['2021-11', 0, 0],
               ['2021-12', 0, 0]
            ]
         },
         xAxis: {
            type: 'category'
         },
         yAxis: [{
            type: 'value',
            axisLabel: {
               formatter: '{value} %'
           },
            axisLine: {
               show: true,
            },
            axisTick: {
               show: true,
            },
            splitNumber: 5

         }],

         series: [{
               type: 'bar'
            },
            {
               type: 'bar'
            },
         ]
      })
   })
   //   统计图结束


   return (
       <div id = "zhu1"></div>
   )
     
}