import {Component} from 'react';
import { Layout, Menu, Breadcrumb} from 'antd';
import {
  DashboardOutlined,
  BellOutlined,
  DisconnectOutlined,
  UserAddOutlined,
  UserOutlined,
  UsergroupAddOutlined,
  BulbOutlined,
  FileProtectOutlined,
  MoneyCollectOutlined,
  QrcodeOutlined ,
} from '@ant-design/icons';

import './nav.css'
import Man1 from '../view/man1'
import Man2 from '../view/man2'
import Man3 from '../view/man3'
import Man4 from '../view/man4'
import Man5 from '../view/man5'
import Man6 from '../view/man6'
import Man7 from '../view/man7'
import Man8 from '../view/man8'
import Man9 from '../view/man9'
import Man10 from '../view/man10'
import Quickly from './quickly'

import {HashRouter,Route,Link,Switch} from 'react-router-dom'

// const IconFont = createFromIconfontCN({
//   scriptUrl: '//at.alicdn.com/t/font_2425577_zoq8e0uflzp.css',
// });
const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

class No2 extends Component {
  state = {
    collapsed: false,
    type : true
  };

  onCollapse = collapsed => {
    console.log(collapsed);
    this.setState({ collapsed });
    this.setState({type:!this.state.type})
    console.log(this.state.type);
    
  };

  render() {
    const { collapsed } = this.state;
    return (
      // <HashRouter>
      <div className='no2'>
        {/* <div className="wrap"> */}
       
          <Layout style={{ minHeight: '100vh',paddingTop:'70px' }}>
            <Sider collapsible collapsed={collapsed} onCollapse={this.onCollapse}>
              <Quickly showType={this.state.type} />
              <div className="logo" />
              <Menu theme="dark" defaultSelectedKeys={['1']} mode="inline">
                <Menu.Item key="1" icon={<DashboardOutlined />}>
                  <Link to="/app/manage">仪表盘</Link>
                </Menu.Item>
                <Menu.Item key="2" icon={<BellOutlined />}>
                  <Link to="/app/manage/man2">待办事项</Link>
                </Menu.Item>
                <Menu.Item key="3" icon={<DisconnectOutlined/>}>
                  <Link to="/app/manage/man3">线索</Link>
                </Menu.Item>
                <Menu.Item key="4" icon={<UserAddOutlined/>}>
                  <Link to="/app/manage/man4">客户</Link>
                </Menu.Item>
                <Menu.Item key="5" icon={<UserOutlined />}>
                  <Link to="/app/manage/man5">联系人</Link>
                </Menu.Item>
                <Menu.Item key="6" icon={<UsergroupAddOutlined />}>
                  <Link to="/app/manage/man6">公海</Link>
                </Menu.Item>
                <Menu.Item key="7" icon={<BulbOutlined  />}>
                  <Link to="/app/manage/man7">商机</Link>
                </Menu.Item>
                <Menu.Item key="8" icon={<FileProtectOutlined />}>
                  <Link to="/app/manage/man8">合同</Link>
                </Menu.Item>
                <Menu.Item key="9" icon={<MoneyCollectOutlined />}>
                  <Link to="/app/manage/man9">回款</Link>
                </Menu.Item>
                <Menu.Item key="10" icon={<QrcodeOutlined />}>
                  <Link to="/app/manage/man10">产品</Link>
                </Menu.Item>
              </Menu>
            </Sider>
            <Layout className="site-layout">
              <Content style={{ margin: '0 16px' }}>
                <div className="site-layout-background" style={{ padding: 24,minWidth:1050, height:"100%"}}>
                  <Switch>
                      <Route path="/app/manage" component={Man1} exact/>
                      <Route path="/app/manage/man2" component={Man2}/>
                      <Route path="/app/manage/man3" component={Man3}/>
                      <Route path="/app/manage/man4" component={Man4}/>
                      <Route path="/app/manage/man5" component={Man5}/>
                      <Route path="/app/manage/man6" component={Man6}/>
                      <Route path="/app/manage/man7" component={Man7}/>
                      <Route path="/app/manage/man8" component={Man8}/>
                      <Route path="/app/manage/man9" component={Man9}/>
                      <Route path="/app/manage/man10" component={Man10}/>
                  </Switch>
                </div>
              </Content>
            </Layout>
          </Layout>
        </div>
        // </HashRouter>
      // </div>
  
    );
  }
}
export default No2
