import './demo7-count.css'
import {useEffect,useState} from 'react'
import * as echarts from 'echarts/core';
import axios from 'axios'
import { Table } from 'antd';
import {
    GaugeChart
} from 'echarts/charts';
import {
    CanvasRenderer
} from 'echarts/renderers';
echarts.use(
    [GaugeChart, CanvasRenderer]
);
let Demo7Count = function(props){
    // console.log(props);
    const columns = [
        {
          title: '姓名',
          dataIndex: 'name',
          key: '1',
          width: 150,
        },
        {
          title: '任务总数',
          dataIndex: 'address',
          key: '2',
          width: 150,
        },
        {
          title: '已完成数',
          dataIndex: 'address',
          key: '3',
          width: 150,
        },
        {
          title: '未完成数',
          dataIndex: 'address',
          key: '4',
          width: 150,
        },
        {
          title: '逾期数',
          dataIndex: 'address',
          key: '5',
          width: 150,
        },
        {
          title: '完成率',
          dataIndex: 'address',
          key: '6',
          width: 150,
        },
      ];
    useEffect(()=>{
        axios.post('http://crm.cimns.com/index.php/work/work/statistic',{
            work_id : props.work_id
        }).then((data)=>{
            // console.log(data.data.data.ownerList);
            setdataList([...data.data.data.ownerList])
        })
        var myChart = echarts.init(document.getElementById('main'));
        var option;

        option = {
            series: [{
                type: 'gauge',
                startAngle: 90,
                endAngle: -270,
                pointer: {
                    show: false
                },
                progress: {
                    show: true,
                    overlap: false,
                    roundCap: true,
                    clip: false,
                    itemStyle: {
                        borderWidth: 1,
                        borderColor: '#464646'
                    }
                },
                axisLine: {

                    lineStyle: {
                        width: 5
                    }
                },
                splitLine: {
                    show: false,
                    distance: 0,
                    length: 10
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    show: false,
                    distance: 50
                },
                data: [{
                    value: 0,
                    name: '完成率',
                    title: {
                        offsetCenter: ['0%', '-30%']
                    },
                },
                ],
                title: {
                    fontSize: 14
                },
                detail: {
                    height: 14,
                    fontSize: 14,
                    color: 'auto',
                    borderColor: 'auto',
                    borderRadius: 20,
                    // borderWidth: 1,
                    formatter: '{value}%'
                }
            }]
        };
        option && myChart.setOption(option);
        var myChart1 = echarts.init(document.getElementById('main2'));
        var option1;

        option1 = {
            series: [{
                type: 'gauge',
                startAngle: 90,
                endAngle: -270,
                pointer: {
                    show: false
                },
                progress: {
                    show: true,
                    overlap: false,
                    roundCap: true,
                    clip: false,
                    itemStyle: {
                        borderWidth: 1,
                        borderColor: '#464646'
                    }
                },
                axisLine: {

                    lineStyle: {
                        width: 5
                    }
                },
                splitLine: {
                    show: false,
                    distance: 0,
                    length: 10
                },
                axisTick: {
                    show: false
                },
                axisLabel: {
                    show: false,
                    distance: 50
                },
                data: [{
                    value: 0,
                    name: '逾期率',
                    title: {
                        offsetCenter: ['0%', '-30%']
                    },
                },
                ],
                title: {
                    fontSize: 12
                },
                detail: {
                    height: 14,
                    fontSize: 14,
                    color: 'auto',
                    borderColor: 'auto',
                    borderRadius: 20,
                    formatter: '{value}%'
                }
            }]
        };
        option1 && myChart1.setOption(option1);
        var myChart3 = echarts.init(document.getElementById('main3'));
        var option3;

        option3 = {
            tooltip: {
                trigger: 'axis',
                axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                    type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
                }
            },
            grid: {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            },
            xAxis: [
                {
                    type: 'category',
                    data: ['总任务', '未完成', '已完成', '已逾期', '已归档'],
                    axisTick: {
                        alignWithLabel: true
                    }
                }
            ],
            yAxis: [
                {
                    type: 'value'
                }
            ],
            grid:{
                height : '70%',
                top: 20
            },
            series: [
                {
                    name: '直接访问',
                    type: 'bar',
                    barWidth: '60%',
                    data: []
                }
            ]
        };

        option3 && myChart3.setOption(option3);

        var myChart4 = echarts.init(document.getElementById('main4'));
        var option4;

        option4 = {
            legend: {
                data: ['已完成', '未完成'],
                bottom:0
            },
            xAxis: [
                {
                    type: 'category',
                    data: ['要做', '在做', '待定']
                }
            ],
            yAxis: [
                {
                    type: 'value'
                }
            ],
            grid:{
                height : '62%',
                top: 20
            },
            series: [
                {
                    name: '已完成',
                    type: 'bar',
                    data: [0,0,0],
                    
                },
                {
                    name: '未完成',
                    type: 'bar',
                    data: [0,0,0],
                    
                }
            ]
        };

        option4 && myChart4.setOption(option4);
        var myChart5 = echarts.init(document.getElementById('main5'));
        var option5;

        option5 = {
            legend: {
                data: ['已完成', '未完成'],
                bottom:0
            },
            xAxis: [
                {
                    type: 'category',
                    data: []
                }
            ],
            yAxis: [
                {
                    type: 'value'
                }
            ],
            grid:{
                height : '65%',
                top: 30
            },
            series: [
                {
                    name: '已完成',
                    type: 'bar',
                    data: [],
                    
                },
                {
                    name: '未完成',
                    type: 'bar',
                    data: [],
                    
                }
            ]
        };

        option5 && myChart5.setOption(option5);
    },[props.work_id])
      const data = [];
      const [dataList,setdataList] = useState([])
    //   console.log(dataList);
      for (let i = 0; i < dataList.length; i++) {
        // console.log(dataList[i]);
        data.push({
            key: dataList[i].id,
            name: dataList[i].realname,
            address: '--',
        });
      }
    return(
        <div className="demo7-count-box">
            <div className="demo7-count">
                <div className="overview">
                    <p style={{height:2}}></p>
                    <p className="demo7-count-wraptitle">任务总览</p>
                    <div className="overview-cont">
                        <div className="overview-cont-left">
                            <div id="main" style={{height:140,width:160}}></div>
                            <div id="main2" style={{height:140,width:160}}></div>
                        </div>
                        <div id="main3" style={{height:140,width:250}}></div>
                        <div className="demo7-count-userBox">
                            <div className="demo7-count-userImg">
                                <img style={{display:props.member.thumb_img===''?'none':'block'}} src={props.member.thumb_img} alt=""></img>
                                <span style={{display:props.member.thumb_img===''?'block':'none'}}>{props.member.realname}</span>
                            </div>
                            <p className="demo7-count-userName">{props.member.realname}</p>
                            <p>项目负责人</p>
                        </div>
                    </div>
                </div>
                <div className="demo7-count-mid">
                    <div>
                        <p className="demo7-count-wraptitle">任务列表</p>
                        <div id="main4" style={{height:180,width:550}}></div>
                    </div>
                    <div>
                        <p className="demo7-count-wraptitle">标签分析</p>
                        <div id="main5" style={{height:180,width:550}}></div>
                    </div>
                </div>
                <div className="demo7-count-footer">
                    <p style={{height:2}}></p>
                    <p className="demo7-count-wraptitle">成员完成状况</p>
                    <div className="demo7-table">
                        <Table columns={columns} dataSource={data} sticky pagination />
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Demo7Count