// import axios from 'axios'
import React,{useEffect,useState} from 'react'
// import {Component} from 'react';
import { Menu,Button  } from 'antd';
import {LeftOutlined, RightOutlined,FilterOutlined, TeamOutlined,UserOutlined ,AppstoreAddOutlined,SignalFilled,LineChartOutlined,FundOutlined,AimOutlined} from '@ant-design/icons';
import {HashRouter,Switch,Route,Link} from 'react-router-dom'
import '../components/wrap.css'
import No1 from '../s-route-view1/no1'
import No2 from '../s-route-view1/no2'
import No3 from '../s-route-view1/no3'
import No4 from '../s-route-view1/no4'
import No5 from '../s-route-view1/no5'
import No6 from '../s-route-view1/no6'
import No7 from '../s-route-view1/no7'
import No8 from '../s-route-view1/no8'
import No9 from '../s-route-view1/no9'
import No10 from '../s-route-view1/no10'
import No11 from '../s-route-view1/no11'
import No12 from '../s-route-view1/no12'
import No13 from '../s-route-view1/no13'
import No14 from '../s-route-view1/no14'
import No15 from '../s-route-view1/no15'
import No16 from '../s-route-view1/no16'
import No17 from '../s-route-view1/no17'
import No18 from '../s-route-view1/no18'
import No19 from '../s-route-view1/no19'
import No20 from '../s-route-view1/no20'
import No21 from '../s-route-view1/no21'
import No22 from '../s-route-view1/no22'
import No23 from '../s-route-view1/no23'
import No24 from '../s-route-view1/no24'
import No25 from '../s-route-view1/no25'
import No26 from '../s-route-view1/no26'
import No27 from '../s-route-view1/no27'
import No28 from '../s-route-view1/no28'
import No29 from '../s-route-view1/no29'
import No30 from '../s-route-view1/no30'
import No31 from '../s-route-view1/no31'


import './commercial.css'


const { SubMenu } = Menu;
const rootSubmenuKeys = ['sub1', 'sub2', 'sub4','sub5','sub6','sub7','sub8','sub9',];



const Commercial = () => {
  const [openKeys, setOpenKeys] = React.useState(['sub1']);

  const onOpenChange = keys => {
    const latestOpenKey = keys.find(key => openKeys.indexOf(key) === -1);
    if (rootSubmenuKeys.indexOf(latestOpenKey) === -1) {
      setOpenKeys(keys);
    } else {
      setOpenKeys(latestOpenKey ? [latestOpenKey] : []);
    }
  };
  const [collapsed,setcollapsed] = useState(false)
  function toggleCollapsed() {
   setcollapsed(!collapsed)
   
 };
  return (
     <div className="max">
        {/* <HashRouter> */}
      <div className="Daohang">
      <Menu defaultOpenKeys={['sub1']}
       inlineCollapsed={collapsed}
        defaultSelectedKeys={['1']}
         mode="inline"
          openKeys={openKeys}
           theme="dark"
            onOpenChange={onOpenChange}
             style={{paddingTop:'105px', backgroundColor:'#001529',minHeight: '100vh',}}
             >
         <Button type="primary" onClick={toggleCollapsed} style={{margin:'auto', height:48,backgroundColor:'#001529',border:'none',position:'fixed',bottom:0 }}>
            {React.createElement(collapsed ? LeftOutlined : RightOutlined )}
         </Button>
         <SubMenu key="sub1" icon={<TeamOutlined />}  title="员工客户分析 ">
               <Menu.Item key="1"><Link to="/app/commercial/"> 客户总量分析</Link></Menu.Item>
               <Menu.Item key="2"><Link to="/app/commercial/No2"> 客户跟进次数分析</Link> </Menu.Item>
               <Menu.Item key="3"><Link to="/app/commercial/No3"> 客户跟进方式分析</Link> </Menu.Item>
               <Menu.Item key="4"><Link to="/app/commercial/No4">客户传化率分析</Link> </Menu.Item>
               <Menu.Item key="5"><Link to="/app/commercial/No5">公海客户分析率</Link> </Menu.Item>
               <Menu.Item key="6"><Link to="/app/commercial/No6">成交周期分析</Link> </Menu.Item>
            </SubMenu>
            <SubMenu key="sub2" icon={<FilterOutlined />} title="销售漏斗分析 ">
            
               <Menu.Item key="7"><Link to="/app/commercial/No7">销售漏斗</Link> </Menu.Item>
               <Menu.Item key="8"><Link to="/app/commercial/No8">新增商机分析</Link> </Menu.Item>
               <Menu.Item key="9"><Link to="/app/commercial/No9">商机传化率分析</Link> </Menu.Item>
            </SubMenu>
            <SubMenu key="sub4" icon={<LineChartOutlined />} title="员工业绩分析 ">
            
               <Menu.Item key="10"><Link to="/app/commercial/No10">合同数量分析</Link> </Menu.Item>
               <Menu.Item key="11"><Link to="/app/commercial/No11">合同金额分析</Link> </Menu.Item>
               <Menu.Item key="12"><Link to="/app/commercial/No12">回款金额分析</Link> </Menu.Item>
               <Menu.Item key="13"><Link to="/app/commercial/No13">合同汇总表</Link> </Menu.Item>
            </SubMenu>
               <SubMenu key="sub5" icon={<UserOutlined />} title="客户画像分析 ">
               
               <Menu.Item key="14"> <Link to="/app/commercial/No14">城市分布分析</Link></Menu.Item>
               <Menu.Item key="15"><Link to="/app/commercial/No15">客户行业分析</Link> </Menu.Item>
               <Menu.Item key="16"> <Link to="/app/commercial/No16">客户级别分析</Link></Menu.Item>
               <Menu.Item key="17"> <Link to="/app/commercial/No17">客户来源分析</Link></Menu.Item>
            </SubMenu>
            <SubMenu key="sub6" icon={<AppstoreAddOutlined />} title="产品分析 ">
            
               <Menu.Item key="18"><Link to="/app/commercial/No18">产品销售情况统计</Link> </Menu.Item>
               <Menu.Item key="19"><Link to="/app/commercial/No19">产品分类销量分析</Link> </Menu.Item>
            
            </SubMenu>
            <SubMenu key="sub7" icon={<SignalFilled />} title="排行榜 ">
            
               <Menu.Item key="20"><Link to="/app/commercial/No20">合同金额排行</Link> </Menu.Item>
               <Menu.Item key="21"><Link to="/app/commercial/No21">回款金额排行</Link> </Menu.Item>
               <Menu.Item key="22"><Link to="/app/commercial/No22">签约合同排行</Link> </Menu.Item>
               <Menu.Item key="23"><Link to="/app/commercial/No23">产品销量排行</Link> </Menu.Item>
               <Menu.Item key="24"><Link to="/app/commercial/No24">新增客户数排行</Link> </Menu.Item>
               <Menu.Item key="25"><Link to="/app/commercial/No25">新增联系人排行</Link> </Menu.Item>
               <Menu.Item key="26"> <Link to="/app/commercial/No26">跟进次数排行</Link></Menu.Item>
               <Menu.Item key="27"><Link to="/app/commercial/No27">跟进客户排行</Link> </Menu.Item>
               <Menu.Item key="28"><Link to="/app/commercial/No28">出差次数排行</Link> </Menu.Item>
            </SubMenu>
            <SubMenu key="sub8" icon={<FundOutlined />} title="产品分析 ">
            
               <Menu.Item key="29"><Link to="/app/commercial/No29">日志分析</Link> </Menu.Item>
               <Menu.Item key="30"><Link to="/app/commercial/No30">审批分析</Link> </Menu.Item>  
            </SubMenu>
            <Menu.Item key="31" className="last_box" key="sub9" icon={<AimOutlined />}><Link to="/app/commercial/No31">业绩目标完成情况</Link> </Menu.Item>
      </Menu>
        
      </div>
    <div className="router_box">
    <Switch>
          <Route path="/app/commercial/" component={No1} exact ></Route>
          <Route path="/app/commercial/No2" component={No2} ></Route>
          <Route path="/app/commercial/No3" component={No3} ></Route>
           <Route path="/app/commercial/No4" component={No4} ></Route>
          <Route path="/app/commercial/No5" component={No5} ></Route>
           <Route path="/app/commercial/No6" component={No6} ></Route>
          <Route path="/app/commercial/No7" component={No7} ></Route>
          <Route path="/app/commercial/No8" component={No8} ></Route>
          <Route path="/app/commercial/No9" component={No9} ></Route>
          <Route path="/app/commercial/No10" component={No10} ></Route>
          <Route path="/app/commercial/No11" component={No11} ></Route>
          <Route path="/app/commercial/No12" component={No12} ></Route>
          <Route path="/app/commercial/No13" component={No13} ></Route>
          <Route path="/app/commercial/No14" component={No14} ></Route>
          <Route path="/app/commercial/No15" component={No15} ></Route>
          <Route path="/app/commercial/No16" component={No16} ></Route>
          <Route path="/app/commercial/No17" component={No17} ></Route>
          <Route path="/app/commercial/No18" component={No18} ></Route>
          <Route path="/app/commercial/No19" component={No19} ></Route>
          <Route path="/app/commercial/No20" component={No20} ></Route>
          <Route path="/app/commercial/No21" component={No21} ></Route>
          <Route path="/app/commercial/No22" component={No22} ></Route>
          <Route path="/app/commercial/No23" component={No23} ></Route>
          <Route path="/app/commercial/No24" component={No24} ></Route>
          <Route path="/app/commercial/No25" component={No25} ></Route>
          <Route path="/app/commercial/No26" component={No26} ></Route>
          <Route path="/app/commercial/No27" component={No27} ></Route>
          <Route path="/app/commercial/No28" component={No28} ></Route>
          <Route path="/app/commercial/No29" component={No29} ></Route>
          <Route path="/app/commercial/No30" component={No30} ></Route>
          <Route path="/app/commercial/No31" component={No31} ></Route>

    </Switch>
    </div>
    {/* </HashRouter> */}
    </div>
  );
};


export default Commercial
