import * as echarts from 'echarts';
import axios from 'axios'
import React, {useState,useEffect} from "react";
import './router-view.css'


let A =function(){
   const [arr,setarr] = useState([])

   //人员名单
   axios.post('http://crm.cimns.com/index.php/bi/customer/statistics').then((data)=>{
       if(arr.length<=0){
         let a = [...arr]
         a = [...data.data.data]
         setarr(a)
       }
    },(err)=>{
      console.log(err)
    })

       //表格
    useEffect(()=>{
              //   使用outerHTML属性获取整个table元素的HTML代码（包括<table>标签），然后包装成一个完整的HTML文档，设置charset为urf-8以防止中文乱码
        var html1 = "<html><head><meta charset='utf-8' /></head><body>" + document.querySelector(".bg_max").outerHTML + "</body></html>";
        // 实例化一个Blob对象，其构造函数的第一个参数是包含文件内容的数组，第二个参数是包含文件类型属性的对象
        var blob = new Blob([html1], { type: "application/vnd.ms-excel" });
        var a = document.querySelector(".btn_box");
        // 利用URL.createObjectURL()方法为a元素生成blob URL
        a.href = URL.createObjectURL(blob);
        // 设置文件名
        a.download = "学生成绩表.xls";
    })
   return(
      <div className="border_box">
              
      <div className="max_box">
      <div className="padin">
         <div className="max_select">
            <div className="item_select">
               <select key="a"  >
               <option key ="今天" >今天</option>
                  <option key ="昨天">昨天</option>
                  <option key="本周">本周</option>
                  <option key="上周">上周</option>
                  <option key="本月">本月</option>
                  <option key="上月">上月</option>
                  <option key="今年" selected>今年</option>
                  <option key="去年">去年</option>
                  <option key="自定义">自定义</option>
               </select>
            </div>
            <div className="item_select">
               <select key="b" >
               <option key ="volvo" selected>办公室</option>
               <option key ="saab">打杂部</option>
               </select>
            </div>
            <div className="item_select">
               <select key="c"  >
               <option key="" disabled selected hidden>选择员工</option>
               {arr.map((item,index)=>{
                  return(
                     <option key ={index}>{item.realname}</option>
                  )
               })}
               
               
               </select>
            </div>
               <button className="btn1">搜索</button>
              
         </div>
         <div className="tabl t1">
            <div className="a_box">
            <a  className="btn_box">
               导出
            </a>
            </div>
         
         <div className="no3_1_1_box_content_list bg_max">
                    <div className="no3_1_1_box_content_list_top">
                        <div>员工</div>
                        <div>填写数</div>
                        <div>接收人未读数</div>
                        <div>未评论数</div>
                        <div>已评论数</div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent bg_box">
                        {arr.map((item, index) => {
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item.realname}</div>
                                    <div>{item.customer_num}</div>
                                    <div>{item.deal_customer_num}</div>
                                    <div>{item.deal_receivables_rate}</div>
                                    <div>{item.contract_money}</div>
                                </div>
                            )
                        })}
                    </div>
                </div>

         </div>
         </div>
         
      </div>
   </div>
   )
}

export default A