import * as echarts from 'echarts';
import React, {useState,useEffect} from "react";
let B =function(){
   useEffect (() => {
      var chartDom = document.getElementById('bingl');
      var myChart = echarts.init(chartDom);
      
      
      myChart.setOption({ 
          tooltip: {
              trigger: 'item'
          },
          legend: {
              orient: 'horizontal',
              bottom:0
          },
          series: [
              {
                  // name: '访问来源',
                  type: 'pie',
                  radius: '50%',
                  data: [
                      {value: 0, name: '打电话'},
                      {value: 0, name: '发邮件'},
                      {value: 0, name: '发短信'},
                      {value: 0, name: '见面拜访'},
                      {value: 0, name: '活动'}
                  ],
                  emphasis: {
                      itemStyle: {
                          shadowBlur: 10,
                          shadowOffsetX: 0,
                          shadowColor: 'rgba(0, 0, 0, 0.5)'
                      }
                  }
              }
          ]
         })



   })


   return(
      <div>
         <div className="bing" id="bingl"></div>
      </div>
   )
}

export default B