import './demo8.css'
import {useState, useEffect} from 'react'
import { Input, Popconfirm, message, Button} from 'antd';
import {
    PlusOutlined,
    UsergroupAddOutlined,
    FilterOutlined,
    SettingOutlined
  } from '@ant-design/icons';
import axios from 'axios'
import {  Popover } from 'antd';
let Demo8 = function(props){
    const [json,setjson] = useState({})
    const [arr,searr] = useState([])
    const [index,setindex] = useState(0)
    const [color,setcolor] = useState([{
        backgroundColor:'rgb(83, 211, 151)'
    },{
        backgroundColor:'rgb(32, 193, 189)'
    },{
        backgroundColor:'rgb(88, 218, 218)'
    },{
        backgroundColor:'rgb(15, 201, 231)'
    },{
        backgroundColor:'rgb(52, 152, 219)'
    },{
        backgroundColor:'rgb(69, 134, 255)'
    },{
        backgroundColor:'rgb(137, 131, 243)'
    },{
        backgroundColor:'rgb(174, 161, 234)'
    },{
        backgroundColor:'rgb(255, 102, 153)'
    },{
        backgroundColor:'rgb(242, 77, 112)'
    },{
        backgroundColor:'rgb(255, 111, 111)'
    }])
    const text = 
    <div className="demo6-name demo8-name">
        <p className="hd">标签设置</p>
        <div className="ipt-box">
            <Input placeholder="请输入内容" defaultValue={json.name} />
            <span style={color[index]}></span>
        </div>
        <div className='color'>
            {color.map((item,idx)=>{
                return(
                    <span style={item} onClick={()=>{getindex(idx)}} key={idx}></span>
                )
            })}
        </div>
    </div>
    ;
    useEffect(()=>{
        axios.post('http://crm.cimns.com/index.php/work/tasklable/read',{
            lable_id:props.match.params.id
        }).then((data)=>{
            // console.log(data.data.data);
            setjson({...data.data.data})
        })
        axios.post('http://crm.cimns.com/index.php/work/tasklable/getWokList',{
            lable_id:props.match.params.id
        }).then((data)=>{
            // console.log(data.data.data);
            searr({...data.data.data})
        })
    },[props.match.params.id])
    function confirm(e) {
        console.log(e);
        message.success('Click on Yes');
    }
      
    function cancel(e) {
        console.log(e);
        message.error('Click on No');
    }
    function getindex(i){
        setindex(i)
    }
    function confirm() {
        message.info('Clicked on Yes.');
      }
    return (
        <div className="demo8">
           <div className="demo8-user">
                {json.name}
                <Popconfirm
                    placement="bottomLeft"
                    title={text}
                    onConfirm={confirm}
                    cancelText="取消"
                    okText="保存"
                >
                    <span><SettingOutlined /></span>
                </Popconfirm>
            </div>
            <div className="demo8-cont">
                <div style={{display:arr?'none':'flex',}}>
                    wertyuio
                </div>
                <div className="demo8-empty" style={{display:arr?'flex':'none',}}>
                    <img src="data:image/png;base64," alt="" />
                </div>
            </div>
        </div>
    )
}
export default Demo8