import './App.css';
import Nav from './components/nav';

import axios from 'axios';
axios.interceptors.request.use(config => {
  if(localStorage.login){
    var login = JSON.parse(localStorage.login)
    config.headers = {
      authKey:login.authKey,
      sessionId:login.sessionId
    }
  }
  return config;
});
function App(props) {
  // console.log(props);
  if(!localStorage.login){
    props.history.push('/')
  } else {
    // var login = JSON.parse(localStorage.login)
  }
  return (
    <div className="App">
      <Nav></Nav>
    </div>
  );
}
export default App;