import * as echarts from 'echarts';
import axios from 'axios'
import { Table,DatePicker, Space } from 'antd'
import React, {useState,useEffect} from "react";
import './router-view.css'

export default function Dt(){
   const [arr,setarr] = useState([])
   //人员名单
   axios.post('http://crm.cimns.com/index.php/admin/structures/getSubUserByStructrue').then((data)=>{
       if(arr.length<=0){
         let a = [...arr]
         a = [...data.data.data]
         setarr(a)
         console.log(a);
       }
    },(err)=>{
      console.log(err)
    })

   //  统计图
    useEffect (() => {
      var chartDom = document.getElementById('Shi');
      var myChart = echarts.init(chartDom);
      myChart.setOption({ 
         title: {
            text: '全部客户                                                                                                                           成交客户',
            left: 'center',
            bottom: 0,
            textStyle: {
                color: '#000'
            }
        },
         tooltip: {
            trigger: 'item',
            formatter: '{a} <br/>{b}: {c} ({d}%)'
        },
        legend: {
            data: ['IT/通信/电子', '金融业', '房地产', '商业服务', '贸易', '生产', '运输/物流', '服务业', '文化传媒','政府', '其他'],
            bottom:25
        },
        series: [
            {
                name: '客户行业分析',
                type: 'pie',
                radius: ['45%', '60%'],
                left: '43.3333%',
              
                data: [
                   
                    {value: 0, name: '运输/物流'},
                    {value: 0, name: '服务业'},
                    {value: 0, name: '文化传媒'},
                    {value: 0, name: '政府'},
                    {value: 0, name: '其他'},
                    {value: 0, name: 'IT/通信/电子'},
                    {value: 0, name: '金融业'},
                    {value: 0, name: '商业服务'},
                    {value: 0, name: '贸易'},
                    {value: 0, name: '生产'},
                ]
            },
            {
                name: '客户行业分析',
                type: 'pie',
                radius: ['45%', '60%'],
                labelLine: {
                    length: 30,
                },
                right: '43.3333%',
                data: [
                  {value: 0, name: 'IT/通信/电子'},
                  {value: 0, name: '金融业'},
                  {value: 0, name: '商业服务'},
                  {value: 0, name: '贸易'},
                  {value: 0, name: '生产'},
                  {value: 0, name: '运输/物流'},
                  {value: 0, name: '服务业'},
                  {value: 0, name: '文化传媒'},
                  {value: 0, name: '政府'},
                  {value: 0, name: '其他'},
                ]
            }
        ]
      })
    })
   return(
      <div className="border_box">
              
      <div className="max_box">
      <div className="padin">
         <div className="max_select">
         <div className="item_select">
               <select key="a"  >
               <option key ="今天" >今天</option>
                  <option key ="昨天">昨天</option>
                  <option key="本周">本周</option>
                  <option key="上周">上周</option>
                  <option key="本月">本月</option>
                  <option key="上月">上月</option>
                  <option key="今年" selected>今年</option>
                  <option key="去年">去年</option>
                  <option key="自定义">自定义</option>
               </select>
            </div>
            <div className="item_select">
               <select key="b" >
               <option key ="办公室" selected>办公室</option>
               <option key ="打杂部">打杂部</option>
               </select>
            </div>
            <div className="item_select">
               <select key="c" >
               <option key="5" disabled selected hidden>选择员工</option>
               {arr.map((item,index)=>{
                  return(
                     <option key ={index}>{item.realname}</option>
                  )
               })}
               </select>
            </div>
               <button className="btn1">搜索</button>
            
         </div>
         <div id="Shi"></div>
         <div className="tabl">

         <div className="no3_1_1_box_content_list">
                    <div className="no3_1_1_box_content_list_top">
                        <div>客户行业</div>
                        <div>IT/通信/电子</div>
                        <div>金融业</div>
                        <div>房地产</div>
                        <div>商业服务</div>
                        <div>贸易</div>
                        <div>生产</div>
                        <div>运输/物流</div>
                        <div>服务业</div>
                        <div>文化传媒</div>
                        <div>政府</div>
                        <div>其他</div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent">
                        {['所有客户（个）','成交客户（个）'].map((item, index) => {
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item}</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                    <div>0</div>
                                </div>
                            )
                        })}
                    </div>
                </div>
         </div>
        
         </div>
         
      </div>
   </div>
   )
}

