import './nav.css'
import {Link, Route, Switch} from 'react-router-dom';
import Demo from '../view/demo';
import Commercial from '../view/s-commercial';
import Office from '../view/office';
import Manage from '../view/manage';
import Person from '../view/person';
import {useState} from 'react';
import { Menu,Modal ,Popover } from 'antd';
import {
    UserOutlined,
    BarChartOutlined,
    FolderAddOutlined,
    MailOutlined ,
  } from '@ant-design/icons';

let Nav = function(props){
    const [isModalVisible, setIsModalVisible] = useState(false);
    const showModal = () => {
        setIsModalVisible(true);
    };
    const handleOk = () => {
        setIsModalVisible(false);
        localStorage.clear();
        window.location='http://localhost:3000/#/'
    };
    const handleCancel = () => {
        setIsModalVisible(false);
    };
    const [current,setcurrent] = useState({current: '/app'})
    function handleClick(e){
        // console.log('click ', e);
        let newcurrent = {...current}
        newcurrent = {current:e.key}
        setcurrent(newcurrent);
        // window.location = 'http://localhost:3000/#'+e.key
    }
    const content = (
    <div className="contstyle">
        <p>
            <Link to="/app/person">个人中心</Link>
        </p>
        <p type="primary" onClick={showModal}>
            退出登录
        </p>
        <Modal title="提示" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
            <p>退出登录？</p>
        </Modal>
        <p>版本 V9.3.2.191220</p>
        <div className="btn">进入企业管理后台</div>
    </div>
    );
    const content2 = (
        <div className='cont2style'>
          <p className="title">您暂未开通授权</p>
          <p className='text'>为了给您提供更好的服务支持</p>
          <p className='text'>建议您购买 官方授权</p>
          <div className="btn">
              <span>400-0812-558</span>
          </div>
        </div>
      );
    return (
        <div className="home">
            <div className='header'>
                <div className="left">
                    <div className="logo">
                        <img src="http://crm.cimns.com/static/img/logo.3e34073.png" alt="" />
                    </div>
                    <div className="nav">
                    <Menu onClick={handleClick} selectedKeys={[current.current]} mode="horizontal" subMenuOpenDelay='0'>
                        <Menu.Item key="app" icon={<MailOutlined />}>
                            <Link to='/app'>办公</Link>
                        </Menu.Item>
                        <Menu.Item key="manage" icon={<UserOutlined />}>
                            <Link to='/app/manage'>客户管理</Link>
                        </Menu.Item>
                        <Menu.Item key="commercial" icon={<BarChartOutlined />}>
                            <Link to='/app/commercial'>商业智能</Link>
                        </Menu.Item>
                        <Menu.Item key="demo" icon={<FolderAddOutlined />}>
                            <Link to='/app/demo'>项目管理</Link>
                        </Menu.Item>
                    </Menu>
                    </div>
                </div>
                <div className="right">
                    <Popover content={content2} trigger="hover">
                        <p><span className="opened">开通授权</span></p>
                    </Popover>
                    <div className="demo">
                        <div style={{ clear: 'both', whiteSpace: 'nowrap' }}>
                        <Popover placement="bottom" content={content} trigger="click">
                            <div className='demos'>
                                <p className='name'>理员</p>
                                <span className='arrow'></span>
                            </div>
                        </Popover>
                        </div>
                    </div>
                </div>
            </div>
            <Switch>
                <Route path='/app/manage' component={Manage} />
                <Route path='/app/commercial' component={Commercial} />
                <Route path='/app/person' component={Person} />
                <Route path='/app/demo' component={Demo} />
                <Route path='/app' component={Office}/>
            </Switch>
        </div>
    )
}
export default Nav;