import './demo7.css'
import axios from 'axios'
import {useState,useEffect} from 'react'
import { Tabs , Popover , Table , Drawer ,Popconfirm, message, Input , Menu , DatePicker,
    Space,Button} from 'antd';
import {
    PlusOutlined,
    UsergroupAddOutlined,
    FilterOutlined,
    SettingOutlined,
    AppstoreOutlined, 
    MailOutlined, 
    HistoryOutlined,
    UserOutlined,
  } from '@ant-design/icons';
import Demo6Transfer from './demo6-transfer'
import Demo7Count from './demo7-count'
let Demo7 = function(props){
    // console.log(props.match.params);
    const [json,setjson] = useState({})
    const [json2,setjson2] = useState([])
    const [list,setlist] = useState([])
    const [member,setmember] = useState([])
    const [index, setindex] = useState(0);
    const [type, settype] = useState(false); 
    // member
    const [listName,setlistName] = useState([])
    const [visible, setVisible] = useState(false);
    const [visible2, setVisible2] = useState(false);
    const [arr,setarr] = useState([])
    const [addarr,setaddarr] = useState([])
    
    const { TabPane } = Tabs;
    const { TextArea } = Input;
    const content = (
        <ul className="setlist">
            <li>添加项目成员</li>
            <li>项目设置</li>
            <li>归档项目</li>
            <li>删除项目</li>
            <li>退出项目</li>
        </ul>
    );
    function confirm() {
        message.info('Clicked on Yes.');
    }
    function callback(key) {
        // console.log(key);
    }
    function showDrawer() {
        setVisible(true);
    };
    function onClose() {
        setVisible(false);
    };
    function showDrawer2() {
        setVisible2(true);
    };
    function onClose2() {
        setVisible2(false);
    };
    function getarr(){
        // settoggletype(true)
        axios.post('http://crm.cimns.com/index.php/admin/users/getUserList').then((data)=>{
            // console.log(data.data);
            if(data.data.code == 200){
                let newarr = [...arr]
                newarr = [...data.data.data]
                setarr(newarr)
                // console.log(arr);
            }
        },(err)=>{
            console.log(err);
        })
    }
    const text = <Demo6Transfer tranArr={arr} addarr={addarr} setaddarr={setaddarr}></Demo6Transfer>;
    useEffect(() => {
        axios.post('http://crm.cimns.com/index.php/work/work/read',{
            work_id:props.match.params.id
        }).then((data)=>{
            // console.log(data.data.data);
            setjson({...data.data.data})
        })
        axios.post('http://crm.cimns.com/index.php/work/task/index',{
            work_id:props.match.params.id
        }).then((data)=>{
            // console.log(data.data.data);
            setjson2([...data.data.data])
        })
        axios.post('http://crm.cimns.com/index.php/work/task/archList',{
            work_id:props.match.params.id
        }).then((data)=>{
            // console.log(data.data.data.list);
            setlist([...data.data.data.list])
        })
        axios.post('http://crm.cimns.com/index.php/work/work/ownerList',{
            work_id:props.match.params.id
        }).then((data)=>{
            setmember({...data.data.data[0]})
        })
        axios.post('http://crm.cimns.com/index.php/work/tasklable/index',{
            // work_id:props.match.params.id
        }).then((data)=>{
            setlistName([...data.data.data.list])
        })
    }, [props.match.params])
    // console.log(listName);
    const columns = [
        {
          title: '附件名称',
          dataIndex: 'address',
          key: '1',
          width: 150,
        },
        {
          title: '附件大小',
          dataIndex: 'address',
          key: '2',
          width: 150,
        },
        {
          title: '上传人',
          dataIndex: 'address',
          key: '3',
          width: 150,
        },
        {
          title: '上传时间',
          dataIndex: 'address',
          key: '4',
          width: 150,
        },
        {
          title: '操作',
          dataIndex: 'address',
          key: '5',
          width: 150,
        },
        {
          title: '操作',
          dataIndex: 'address',
          key: '6',
          width: 150,
        },
      ];
      const data = [];
    //   for (let i = 0; i < 100; i++) {
    //     data.push({
    //       key: i,
    //       name: `Edrward ${i}`,
    //       age: 32,
    //       address: `London Park no. ${i}`,
    //     });
    //   }
    function setsm(inx) {
        setindex(inx);
        settype(true);
      }
      function setsmn(inx) {
        setindex(inx);
        settype(false);
      }
      const error = () => {
        message.error("参数错误");
      };
    const { SubMenu } = Menu;
    function handleClick(e){
        console.log('click ', e);
      };
    return(
        <div className="demo7">
            <div className="demo7-top-box">
                <div className="demo7-user-box">
                    <div className="demo7-user">
                        {json.name}
                        <Popover placement="bottomLeft" content={content} trigger="click" className="setlist-box">
                            <span><SettingOutlined /></span>
                        </Popover>
                    </div>
                    <div className="user-tags">
                        <div>
                            <p type="primary" onClick={showDrawer}>
                                <UsergroupAddOutlined />
                            </p>
                            <Drawer
                                title= {<div>成员 (<span>{addarr.length + 1}</span>)</div>}
                                placement="right"
                                closable={false}
                                onClose={onClose}
                                visible={visible}
                                closable={true}
                            >
                                <Popconfirm
                                    placement="bottomLeft"
                                    title={text}
                                    onConfirm={confirm}
                                    okText="确定"
                                    cancelText="取消"
                                >
                                    <p onClick={getarr} className="add">添加成员</p>
                                </Popconfirm>
                                <ul className="demo7-user-list">
                                    <li key={member.id}>
                                        <div className="userImg">
                                            <img style={{display:member.thumb_img===''?'none':'block'}} src={member.thumb_img} alt=""></img>
                                            <span style={{display:member.thumb_img===''?'block':'none'}}>{member.realname}</span>
                                        </div>
                                        <div className="realname">
                                            {member.realname}
                                        </div>
                                    </li>
                                    {addarr.map((item,idx)=>{
                                        return(
                                            <li key={item.id}>
                                                <div className="userImg">
                                                    <img style={{display:item.thumb_img===''?'none':'block'}} src={item.thumb_img} alt=""></img>
                                                    <span style={{display:item.thumb_img===''?'block':'none'}}>{item.realname}</span>
                                                </div>
                                                <div className="realname">{item.realname}</div>
                                            </li>
                                        )
                                    })}
                                </ul>
                            </Drawer>
                        </div>
                        <div>
                            <p type="primary" onClick={showDrawer2}>
                                <FilterOutlined />
                            </p>
                            <Drawer
                                title="任务筛选"
                                placement="right"
                                closable={false}
                                onClose={onClose2}
                                visible={visible2}
                                closable={true}
                            >
                                <Menu
                                    onClick={handleClick}
                                    style={{ }}
                                    defaultSelectedKeys={['1']}
                                    defaultOpenKeys={['sub1']}
                                    mode="inline"
                                    className="menuBox"
                                >
                                    <SubMenu key="sub1" icon={<MailOutlined />} title="成员" className="demo7-user-list">
                                        <Menu.Item key={member.id}>
                                            <div className="userImg">
                                                <img style={{display:member.thumb_img===''?'none':'block'}} src={member.thumb_img} alt=""></img>
                                                <span style={{display:member.thumb_img===''?'block':'none'}}>{member.realname}</span>
                                            </div>
                                            <div className="realname">
                                                {member.realname}
                                            </div>
                                        </Menu.Item>
                                        {addarr.map((item,idx)=>{
                                        return(
                                            <Menu.Item key={item.id}>
                                                <div className="userImg">
                                                    <img style={{display:item.thumb_img===''?'none':'block'}} src={item.thumb_img} alt=""></img>
                                                    <span style={{display:item.thumb_img===''?'block':'none'}}>{item.realname}</span>
                                                </div>
                                                <div className="realname">{item.realname}</div>
                                            </Menu.Item>
                                        )
                                    })}
                                    </SubMenu>
                                    <SubMenu key="sub2" icon={<AppstoreOutlined />} title="截止时间">
                                        <Menu.Item key="5">今天</Menu.Item>
                                        <Menu.Item key="6">明天</Menu.Item>
                                        <Menu.Item key="7">本周</Menu.Item>
                                        <Menu.Item key="8">本月</Menu.Item>
                                        <Menu.Item key="9">未设置截止时间</Menu.Item>
                                        <Menu.Item key="10">已延期</Menu.Item>
                                        <Menu.Item key="11">今日更新</Menu.Item>
                                    </SubMenu>
                                    <SubMenu key="sub4" icon={<SettingOutlined />} title="标签">
                                        {/* <Menu.Item key="50"></Menu.Item> */}
                                        {listName.map((item,idx)=>{
                                            return(
                                                <Menu.Item key={item.create_user_id}>{item.name}</Menu.Item>
                                            )
                                        })}
                                    </SubMenu>
                                </Menu>
                            </Drawer>
                        </div>
                    </div>
                </div>
                <Tabs defaultActiveKey="1" onChange={callback}>
                    <TabPane tab="任务" key="1">
                    <div className="demo7-position">
                        <div className="demo7-cont">
                            {/* <div className="demo7-cont-box"> */}
                            {json2.map((item, idx) => {
                                return (
                                <div className="item" key={item.class_id}>
                                    <div className="info-title">
                                    <p className="demo7-hd">{item.class_name}</p>
                                    <p>0 / 0</p>
                                    </div>
                                    <div className="demo7-line">
                                    <div className="line"></div>
                                    </div>
                                    <div className="demo7-cont-list"></div>
                                    <div className="demo7-ins-small">
                                    <div
                                        className="xinjiansr"
                                        style={{
                                        display:
                                            index === idx
                                            ? type
                                                ? "none"
                                                : "block"
                                            : "block",
                                        }}
                                    >
                                        <div
                                        className="newcreate"
                                        onClick={() => {
                                            setsm(idx);
                                        }}
                                        >
                                        <PlusOutlined />
                                        <p>新建任务</p>
                                        </div>
                                    </div>
                                    <div
                                        className="demo7-ins-item"
                                        style={{
                                        display:
                                            index === idx
                                            ? type
                                                ? "block"
                                                : "none"
                                            : "none",
                                        }}
                                    >
                                        <Input placeholder="请输入内容" />
                                        <div className="demo7-iconse">
                                        <div className="demo7-time">
                                            <HistoryOutlined className="icons" />
                                            <DatePicker />
                                        </div>
                                        <div className="demo7-icon-user">
                                            <UserOutlined className="icons" />
                                        </div>
                                        </div>
                                        <div className="btnsm">
                                        <Button type="primary" onClick={error}>
                                            确认
                                        </Button>
                                        {/* <Button type="primary"></Button> */}
                                        <Button
                                            onClick={() => {
                                            setsmn(idx);
                                            }}
                                        >
                                            取消
                                        </Button>
                                        </div>
                                    </div>
                                    </div>
                                </div>
                                );
                            })}
                            <div className="item last-item">
                                <div className="info-title">
                                <p className="demo7-hd">
                                    <PlusOutlined />
                                    新建列表
                                </p>
                                </div>
                            </div>
                            </div>
                        {/* </div> */}
                        </div>
                    </TabPane>
                    <TabPane tab="附件" key="2">
                        <Table columns={columns} dataSource={data} sticky pagination />
                        {/* <Pagination defaultCurrent={6} total={500} /> */}
                    </TabPane>
                    <TabPane tab="统计任务" key="3">
                        <Demo7Count work_id={props.match.params.id} member={member}></Demo7Count>
                    </TabPane>
                    <TabPane tab="归档任务" key="4">
                        <div className="demo7-empty" style={{display:list.length?'none':'bloak'}}>
                            <img src="data:image/png;base64," alt="" />
                        </div>
                    </TabPane>
                </Tabs>
            </div>
            {/*  */}
        </div>
    )
}
export default Demo7