import * as echarts from 'echarts';
import $ from  'jquery'
import React, {useEffect} from "react";
export default function Dt(){
   useEffect(()=>{
      var ROOT_PATH = 'https://cdn.jsdelivr.net/gh/apache/echarts-website@asf-site/examples';

      var chartDom = document.getElementById('main');
      var myChart = echarts.init(chartDom);
      var option;
      myChart.showLoading();
      $.get(ROOT_PATH + '/data/asset/geo/HK.json', function (geoJson) {
         myChart.hideLoading();
         echarts.registerMap('HK', geoJson);
   
         myChart.setOption(option = {
            title: {
               text: '全部客户',
               left: 'center',
               bottom: 0,
               left:'46%',
               textStyle: {
                   color: '#000'
               }
           },
            tooltip: {
               trigger: 'item',
               formatter: '{b}<br/>{c} (p / km2)'
           },
           toolbox: {
               show: true,
               orient: 'vertical',
               right: '23%',
               top: 'center',
               //保存
               feature: {
                   dataView: {readOnly: false},
                   restore: {},
                   saveAsImage: {}  
               }
           },
           visualMap: {
               min: 0,
               max: 50,
               left:60,
               text: ['多', '少'],
               realtime: false,
               calculable: true,
               inRange: {
                   color: ['lightskyblue', 'yellow', 'orangered']
               }
           },
           series: [
               {
                   name: '香港18区人口密度',
                   type: 'map',
                   mapType: 'HK', // 自定义扩展图表类型
                   label: {
                       show: true
                   },
                   data: [
                       {name: '中西区', value: 20},
                       {name: '湾仔', value: 15},
                       {name: '东区', value: 31},
                       {name: '南区', value: 6},
                       {name: '油尖旺', value: 44},
                       {name: '深水埗', value: 40},
                       {name: '九龙城', value: 37},
                       {name: '黄大仙', value: 45},
                       {name: '观塘', value: 55},
                       {name: '葵青', value: 21},
                       {name: '荃湾', value: 4},
                       {name: '屯门', value: 58},
                       {name: '元朗', value: 4},
                       {name: '北区', value: 22},
                       {name: '大埔', value: 21},
                       {name: '沙田', value: 91},
                       {name: '西贡', value: 33},
                       {name: '离岛', value: 8},
                      
                   ],
                   // 自定义名称映射
                   nameMap: {
                       'Central and Western': '中西区',
                       'Eastern': '东区',
                       'Islands': '离岛',
                       'Kowloon City': '九龙城',
                       'Kwai Tsing': '葵青',
                       'Kwun Tong': '观塘',
                       'North': '北区',
                       'Sai Kung': '西贡',
                       'Sha Tin': '沙田',
                       'Sham Shui Po': '深水埗',
                       'Southern': '南区',
                       'Tai Po': '大埔',
                       'Tsuen Wan': '荃湾',
                       'Tuen Mun': '屯门',
                       'Wan Chai': '湾仔',
                       'Wong Tai Sin': '黄大仙',
                       'Yau Tsim Mong': '油尖旺',
                       'Yuen Long': '元朗',
                   }
               }
           ]
         })

         
         
      })
      // 2
      var chartDom1 = document.getElementById('maaaaa');
      var myChart1 = echarts.init(chartDom1);
      var option;
      myChart1.showLoading();
      $.get(ROOT_PATH + '/data/asset/geo/HK.json', function (geoJson) {
         myChart1.hideLoading();
         echarts.registerMap('HK', geoJson);
   
         myChart1.setOption(option = {
            title: {
               text: '成交客户',
               left: 'center',
               bottom: 0,
               left:'46%',
               textStyle: {
                   color: '#000'
               }
           },
            tooltip: {
               trigger: 'item',
               formatter: '{b}<br/>{c} (p / km2)'
           },
           toolbox: {
               show: true,
               orient: 'vertical',
               right: '23%',
               top: 'center',
               //保存
               feature: {
                   dataView: {readOnly: false},
                   restore: {},
                   saveAsImage: {}  
               }
           },
           visualMap: {
               min: 0,
               max: 50,
               left:60,
               text: ['多', '少'],
               realtime: false,
               calculable: true,
               inRange: {
                   color: ['lightskyblue', 'yellow', 'orangered']
               }
           },
           series: [
               {
                   name: '香港18区人口密度',
                   type: 'map',
                   mapType: 'HK', // 自定义扩展图表类型
                   label: {
                       show: true
                   },
                   data: [
                       {name: '中西区', value: 20},
                       {name: '湾仔', value: 15},
                       {name: '东区', value: 31},
                       {name: '南区', value: 6},
                       {name: '油尖旺', value: 44},
                       {name: '深水埗', value: 40},
                       {name: '九龙城', value: 37},
                       {name: '黄大仙', value: 45},
                       {name: '观塘', value: 55},
                       {name: '葵青', value: 21},
                       {name: '荃湾', value: 4},
                       {name: '屯门', value: 58},
                       {name: '元朗', value: 4},
                       {name: '北区', value: 22},
                       {name: '大埔', value: 21},
                       {name: '沙田', value: 91},
                       {name: '西贡', value: 33},
                       {name: '离岛', value: 8},
                      
                   ],
                   // 自定义名称映射
                   nameMap: {
                       'Central and Western': '中西区',
                       'Eastern': '东区',
                       'Islands': '离岛',
                       'Kowloon City': '九龙城',
                       'Kwai Tsing': '葵青',
                       'Kwun Tong': '观塘',
                       'North': '北区',
                       'Sai Kung': '西贡',
                       'Sha Tin': '沙田',
                       'Sham Shui Po': '深水埗',
                       'Southern': '南区',
                       'Tai Po': '大埔',
                       'Tsuen Wan': '荃湾',
                       'Tuen Mun': '屯门',
                       'Wan Chai': '湾仔',
                       'Wong Tai Sin': '黄大仙',
                       'Yau Tsim Mong': '油尖旺',
                       'Yuen Long': '元朗',
                   }
               }
           ]
         })

         
         
      })
   })
  

   
   // var myCharta = echarts.init()



   return(
      <div className="border_box">     
         <div className="max_box">
            <div className="padin">
               <div className="box_l">
               <div id="main"></div>
               <div id="maaaaa"></div>
               </div>
               
            </div>
         </div>
      </div>
      
   )
}

