import './demo6.css'
import {useState} from 'react'
import { Input,TreeSelect , Button ,Popover , Popconfirm, message, } from 'antd';
import axios from 'axios'
import {
    CloseOutlined,
    PlusOutlined
} from '@ant-design/icons';
import Demo6Transfer from './demo6-transfer'

let Demo6 = function (props) {
    // console.log(props);
    const [value,setvalue] = useState(undefined)
    const [size,setsize] = useState('default')
    const [index,setindex] = useState(0)
    const [arr,setarr] = useState([])
    const [addarr,setaddarr] = useState([])
    // const [toggletype,settoggletype] = useState(false)
    // showhide={toggletype} setshowhide={settoggletype}
    const text = <Demo6Transfer tranArr={arr} addarr={addarr} setaddarr={setaddarr}></Demo6Transfer>;

    function confirm() {
        message.info('Clicked on Yes.');
    }
    
    const [color,setcolor] = useState([{
        backgroundColor:'rgb(83, 211, 151)'
    },{
        backgroundColor:'rgb(32, 193, 189)'
    },{
        backgroundColor:'rgb(88, 218, 218)'
    },{
        backgroundColor:'rgb(15, 201, 231)'
    },{
        backgroundColor:'rgb(52, 152, 219)'
    },{
        backgroundColor:'rgb(69, 134, 255)'
    },{
        backgroundColor:'rgb(137, 131, 243)'
    },{
        backgroundColor:'rgb(174, 161, 234)'
    },{
        backgroundColor:'rgb(255, 102, 153)'
    },{
        backgroundColor:'rgb(242, 77, 112)'
    },{
        backgroundColor:'rgb(255, 111, 111)'
    }])
    const treeData = [
        {
          title: '私有：只有加入的成员才能看见此项目',
          value: '0-0',
        },
        {
          title: '公开：企业所有成员都可以看见此项目',
          value: '0-1',
        },
      ];
    const { TextArea } = Input;
    // const text = <span>Title</span>;
    
    function onChange(value){
        console.log(value);
        setvalue(value)
    }
    function back(){
        props.history.go(-1)
    }
    function OK(){
        props.history.go(-1)
    }
    function cancel(){
        props.history.go(-1)
    }
    function getindex(i){
        setindex(i)
    }
    function getarr(){
        // settoggletype(true)
        axios.post('http://crm.cimns.com/index.php/admin/users/getUserList').then((data)=>{
            // console.log(data.data);
            if(data.data.code == 200){
                let newarr = [...arr]
                newarr = [...data.data.data]
                setarr(newarr)
                // console.log(arr);
            }
        },(err)=>{
            console.log(err);
        })
    }
    // const content = (
    //     <div>
    //         <Demo6Transfer tranArr={arr} addarr={addarr} setaddarr={setaddarr} showhide={toggletype} setshowhide={settoggletype}></Demo6Transfer>
    //     </div>
    // );
    return(
        <div className="demo6">
            <div className="demo6-cont">
                <div className="demo6-title">
                    <p className="bd">创建项目</p>
                    <p onClick={back}><CloseOutlined /></p>
                </div>
                <div className="demo6-mid">
                    <div className="demo6-name">
                        <p className="hd">项目名称</p>
                        <div className="ipt-box">
                            <Input placeholder="请输入内容" />
                            <span style={color[index]}></span>
                        </div>
                        <div className='color'>
                            {color.map((item,idx)=>{
                                return(
                                    <span style={item} onClick={()=>{getindex(idx)}} key={idx}></span>
                                )
                            })}
                        </div>
                    </div>
                    <div className="demo6-info">
                        <p className="title">可见范围</p>
                        <TextArea rows={4} placeholder="请输入内容" />
                    </div>
                    <div className="demo6-see">
                        <p className="title">可见范围</p>
                        <TreeSelect
                            style={{ width: '100%' }}
                            value={value}
                            dropdownStyle={{ maxHeight: 400, overflow: 'auto' }}
                            treeData={treeData}
                            // value={treeData[0].title}
                            defaultValue={treeData[0].title}
                            treeDefaultExpandAll
                            onChange={onChange}
                        />
                    </div>
                    <div className="demo6-member">
                    <p className="title">项目成员</p>
                    <div className="list-box">
                        <ul className="demo6-list">
                            {addarr.map((item,idx)=>{
                                return(
                                    <li key={item.id} className="userImg">
                                        <img style={{display:item.thumb_img===''?'none':'block'}} src={item.thumb_img} alt=""></img>
                                        <span style={{display:item.thumb_img===''?'block':'none'}}>{item.realname}</span>
                                    </li>
                                )
                            })}
                            <li className="member-add">
                                {/* <Popover placement="bottomLeft" content={toggletype===true?content:''} trigger="click">
                                    <Button onClick={getarr}> <PlusOutlined /></Button>
                                </Popover> */}
                                <Popconfirm
                                    placement="bottomLeft"
                                    title={text}
                                    onConfirm={confirm}
                                    okText="确定"
                                    cancelText="取消"
                                >
                                    <Button onClick={getarr}><PlusOutlined /></Button>
                                </Popconfirm>
                            </li>
                        </ul>
                    </div>
                    </div>
                </div>
                <div className="demo6-footer">
                    <Button type="primary" size={size} onClick={OK}>
                        确定
                    </Button>
                    <Button size={size} onClick={cancel}>取消</Button>
                </div>
            </div>
        </div>
    )
}
export default Demo6;