import './demo6-transfer.css'
import {useState} from 'react'
import { Button } from 'antd'
import {
  CloseOutlined,
  ZoomInOutlined
} from '@ant-design/icons';
let DemoTransfer = function(props) {
  // console.log(props);
  const [arr2,setarr2] = useState([])
//   const [index,setindex] = useState(0)
function gettype(item){
    let type = true;
    let newarr2 = [...arr2]
    
    for(let j = 0;j<newarr2.length;j++){
        if(newarr2[j].id === item.id){
            newarr2.splice(j,1)
            setarr2(newarr2)
            type = false
        }
    }
    if(type){
        for(let i = 0;i<props.tranArr.length;i++){
            if(props.tranArr[i].id === item.id){
                newarr2.push(props.tranArr[i])
                setarr2(newarr2)
            }
        } 
    }
  }
  function ok(){
      props.setaddarr([...arr2])
  }
  function empty(){
    setarr2([])
  }
    return (
      <div className="transfer">
        <div className="title-box">
          <p className="title">选择成员</p>
          <p className="icon"><CloseOutlined /></p>
        </div>
        <div className="tran-cont">
          <div className="tran-left">
            <div className="hd-box">
              <p>员工</p>
            </div>
            <div className="tran-member-box">
              <div className="search-box">
                <input placeholder="搜索员工" />
                <span><ZoomInOutlined /></span>
              </div>
              <div className="tran-member">
                {props.tranArr.map((item,idx)=>{
                  return(
                    <div key={item.id} className='tran-list' onClick={()=>{gettype(item)}}>
                      {/* <div className="checkbox">
                          <input type='check' />
                      </div> */}
                      <div className="userImg">
                          <img style={{display:item.thumb_img===''?'none':'block'}} src={item.thumb_img} alt=""></img>
                          <span style={{display:item.thumb_img===''?'block':'none'}}>{item.realname}</span>
                      </div>
                      <p className="username">{item.username}{item.id}</p>
                    </div>
                  )
                })}
              </div>
            </div>
          </div>
          <div className="tran-right">
            <div className="hd-box">
              <p className="select">已选择员工({arr2.length})部门(0)</p>
              <p onClick={empty}>清空</p>
            </div>
            <div className="tran-member-box">
              <div className="tran-member">
                {arr2.map((item,idx)=>{
                    return(
                        <div key={item.id} className='tran-list' onClick={()=>{gettype(item)}}>
                        {/* <div className="checkbox">
                            <input type='checkbox'/>
                        </div> */}
                        <div className="userImg">
                            <img style={{display:item.thumb_img===''?'none':'block'}} src={item.thumb_img} alt=""></img>
                            <span style={{display:item.thumb_img===''?'block':'none'}}>{item.realname}</span>
                        </div>
                        <p className="username">{item.username}{item.id}</p>
                        </div>
                    )
                })}
              </div>
            </div>
          </div>
        </div>
        <div className="tran-footer">
          <Button type="primary" onClick={ok}>确定</Button>
          {/* <Button>取消</Button> */}
        </div>
      </div>
    );
}
export default DemoTransfer;