
import * as echarts from 'echarts';
import axios from 'axios'
import { Table } from 'antd'
import React, {useState} from "react";
import './router-view.css'
import Bing from './s-zi-router/bing'
import Zhu from './s-zi-router/zhu'




let San =function(){


   const [arr,setarr] = useState([])
   const [arr1,setarr1] = useState([])
   //人员名单
    axios.post('http://crm.cimns.com/index.php/admin/structures/getSubUserByStructrue').then((data)=>{
       if(arr.length<=0){
         let a = [...arr]
         a = [...data.data.data]
         setarr(a)
       }
    },(err)=>{
      console.log(err)
    })


    axios.post('http://crm.cimns.com/index.php/bi/customer/recordMode').then((data)=>{
       if(arr1.length<=0){
         let a = [...arr1]
         a = [...data.data.data]
         setarr1(a)
       }
    },(err)=>{
      console.log(err)
    })

   // 组件切换  开关
      const [type,settype] = useState(true)
    function Qh(val){
       console.log(val);
    }
      

      
   return(
      <div className="border_box">
              
      <div className="max_box">
      <div className="padin">
         <div className="max_select">
            <div className="item_select">
               <select key="a" defaultValue="">
               <option key ="今天" >今天</option>
               <option key ="昨天">昨天</option>
               <option key="本周">本周</option>
               <option key="上周">上周</option>
               <option key="本月">本月</option>
               <option key="上月">上月</option>
               <option key="今年" selected>今年</option>
               <option key="去年">去年</option>
               <option key="自定义">自定义</option>
               </select>
            </div>
            <div className="item_select">
               <select key="b" defaultValue="" >
               <option key ="办公室" selected>办公室</option>
               <option key ="打杂部">打杂部</option>
               </select>
            </div>
            <div className="item_select">
               <select key="c" defaultValue="" >
               <option key="456" disabled selected hidden>选择员工</option>
               {arr.map((item,index)=>{
                  return(
                     <option key={index+item.realname}>{item.realname}</option>
                  )
               })}
               </select>
            </div>
            <div className="item_select" >
               <select key="d" defaultValue="" onChange={()=>{
                     settype(!type)
                     console.log(type);
                     
                  }}>
                  <option value ="饼状图" selected id="Bing_1">饼状图</option>
                  <option value ="柱状图"  >柱状图</option>
               </select>
            </div>
               <button className="btn1">搜索</button>
            
         </div>
         <div className="mains">
               <div className="tab_1"  style={{opacity:type?'1':'0',zIndex:type?'99':'1'}}>
                  <Bing></Bing>
               </div>
               <div className="tab_2"  style={{opacity:type?'0':'1',zIndex:type?'1':'99'}}>
                  <Zhu></Zhu>
               </div>
         </div>
         <div className="tabl">

         <div className="no3_1_1_box_content_list">
                    <div className="no3_1_1_box_content_list_top">
                        <div>跟进方式</div>
                        <div>个数</div>
                        <div>占比（%）</div>
                    </div>
                    <div className="no3_1_1_box_content_list_ciontent">
                        {arr1.map((item, index) => {
                           // console.log(item);
                           
                            return (
                                <div className="no3_1_1_box_content_list_ciontent_item" key={index}>
                                    <div>{item.category}</div>
                                    <div>{item.proportion}</div>
                                    <div>{item.recordNum}</div>
                                </div>
                            )
                        })}
                    </div>
                </div>
         </div>
         </div>
         
      </div>
   </div>
   )
}

export default San

