import { Form, Input, Button } from 'antd';
import axios from 'axios'
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import {useState} from 'react'
import './login.css'
let Login = function(props){
    const onFinish = (values: any) => {
        console.log('Received values of form: ', values);
      };
      let [json,setjson] = useState({username:'',password:''})
      function getval(event){
          let dom = event.target;
          let type = dom.getAttribute('login')
        //   console.log(type);
          let val = dom.value.split(' ').join('');
        //   console.log(val);
          let newjson = {...json}
          newjson[type] = val
          setjson(newjson)
      }
      function submit(){
          if(json.username === '' || json.password === ''){
              console.log('请输入内容');
          }else{
              axios.post('http://crm.cimns.com/index.php/admin/base/login',json).then((data)=>{
                  if(data.data.code === 200){
                      localStorage.login = JSON.stringify(data.data.data)
                      props.history.push('/app')
                  }else if(data.data.code === 400){
                      console.log('账号或密码不正确');
                  }
              })
          }
      }
    return (
        <div className="login">
            <div className="img">
                <img src='http://crm.cimns.com/static/img/login.bc884f1.png' alt='' />
            </div>
            <div className="login-box">
                <div className="login_in">
                    <p className="hd">悟空CRM</p>
                    <Form
                    name="normal_login"
                    className="login-form"
                    initialValues={{ remember: true }}
                    onFinish={onFinish}
                    >
                        <Form.Item
                            name="username"
                            rules={[{ required: true, message: '请输入你的用户名' }]}
                        >
                            <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="请输入用户名" login="username" value={json.username} onChange={getval} />
                        </Form.Item>
                        <Form.Item
                            name="password"
                            rules={[{ required: true, message: '请输入你的密码' }]}
                        >
                            <Input
                            prefix={<LockOutlined className="site-form-item-icon" />}
                            type="password"
                            placeholder="请输入密码"
                            login="password" value={json.username} onChange={getval}
                            />
                        </Form.Item>
                        <Form.Item>
                            <Button type="primary" htmlType="submit" className="login-form-button" onClick={submit}>
                            登录
                            </Button>
                        </Form.Item>
                    </Form>
                </div>
                <div className="info">
                    <p className="text">悟空CRM受国家计算机软件著作权保护，未经授权不得进行商业行为，违者必究。</p>
                    <p className="text">©2019 悟空CRM</p>
                </div>
            </div>
            <div className="pic">
                <img src='http://crm.cimns.com/static/img/logo.3e34073.png' alt='' />
            </div>
        </div>
    )
}

export default Login