import { HashRouter } from "react-router-dom"
import './man9.css'
import { useState } from 'react';
import { Input, Space, Menu, Dropdown, Button, message, Modal, DatePicker, Select, Table, Pagination } from 'antd';
import { AudioOutlined, DownOutlined, ExclamationCircleOutlined, CloseCircleOutlined, SettingOutlined } from '@ant-design/icons';
const { Search } = Input;
const { confirm } = Modal;
const menu = (
  <Menu onClick={handleMenuClick}>
    <Menu.Item key="1">
      导入
      </Menu.Item>
    <Menu.Item key="2">
      导出
      </Menu.Item>
  </Menu>
);
const menuu = (
  <Menu onClick={handleMenuClick}>
    <Menu.Item key="1">
      我负责的回款
    </Menu.Item>
    <Menu.Item key="2">
      下属负责的回款
    </Menu.Item>
    <Menu.Item key="3">
      全部回款
    </Menu.Item>
    <Menu.Item key="4">
      +新建场景
    </Menu.Item>
    <Menu.Item key="5">
      +管理
    </Menu.Item>
  </Menu>
);
function onChange(date, dateString) {
  console.log(date, dateString);
}
function handleButtonClick(e) {
  message.info('Click on left button.');
}

function handleMenuClick(e) {
  message.info('Click on menu item.');
  // console.log('click', e);
}
function showConfirm() {
  confirm({
    title: '提示',
    icon: <ExclamationCircleOutlined />,
    content: '您确定要删除这一条数据吗？',
    onOk() {
      console.log('OK');
      message.success('已添加')
    },
    onCancel() {
      console.log('Cancel');
      message.warning('已取消删除');
    },
    okText: '确定',
    cancelText: '取消',

  });
}
const suffix = (
  <AudioOutlined
    style={{
      fontSize: 16,
      color: '#1890ff',
    }}
  />
);
const onSearch = value => console.log(value);
const provinceDhh = ['支票', '现金', '邮政汇款', '电汇', '网上转账', '支付宝', '微信支付', '其他']
const handleProvinceChange = value => { };
const { Option } = Select;
const { TextArea } = Input;
const provinceD = ['等于', '不等于', '包含', '不包含', '开始于', '结束于', '为空', '不为空', '大于', '大于等于', '小于', '小于等于'];
const provinceDa = ['客户名称', '客户级别', '客户行业', '客户来源', '成交状态', '电话', '网址', '下次联系时间', '备注', '手机', '创建人', '更新时间', '创建时间', '负责人', '地区定位'];
const columns = [
  {
    title: <div>
      <div style={{ width: "14px", height: "14px", float: "left", border: "1px #ddd solid", margin: "4px 9px 0 0" }}></div>
     回款编号
    </div>,
    dataIndex: 'age',
    defaultSortOrder: 'descend',
    sorter: (a, b) => a.age - b.age,
    fixed: 'left',
    width: 100,
  },
  {
    title: '客户名称',
    dataIndex: 'age',
    defaultSortOrder: 'descend',
    sorter: (a, b) => a.age - b.age,
    dataIndex: 'address', key: '13',
    width: 100,
  },
  {
    title: '合同编号',
    defaultSortOrder: 'descend',
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '1',
    width: 100,
  },
  {
    title: '回款编号',
    defaultSortOrder: 'descend',
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '2',
    width: 100,
  },
  {
    title: '回款日期', faultSortOrder: 'descend', width: 100,
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '3'
  },

  {
    title: '回款方式', faultSortOrder: 'descend', width: 100,
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '4'
  },

  {
    title: '回款金额', faultSortOrder: 'descend', width: 100,
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '5'
  },

  {
    title: '期数', faultSortOrder: 'descend', width: 120,
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '6'
  },
  {
    title: '备注', faultSortOrder: 'descend', width: 100,
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '7'
  },
  {
    title: '创建人', faultSortOrder: 'descend', width: 100,
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '8'
  },
  {
    title: '更新时间', faultSortOrder: 'descend', width: 100,
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '9'
  },
  {
    title: '创建时间', faultSortOrder: 'descend', width: 100,
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '10'
  },
  {
    title: '负责人', faultSortOrder: 'descend', width: 100,
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '11'
  },
  {
    title: '合同金额', faultSortOrder: 'descend', width: 100,
    sorter: (a, b) => a.age - b.age, dataIndex: 'address', key: '12'
  },
  {
    title: '', faultSortOrder: 'descend', width: 100,
    dataIndex: 'address', key: '14'
  },
  {
    title: <div style={{ marginTop: "10px" }}>
      状态
       <SettingOutlined style={{ marginLeft: "13px", marginTop: "5px", float: "right" }} />
    </div>,
    key: 'operation',
    fixed: 'right',
    width: 90,
    render: () => <a>action</a>,
  },
]


let Man9 = function (params) {
  const [visible, setVisible] = useState(false);
  const [visiblea, setVisiblea] = useState(false);
  return (
    <HashRouter>
      <div>
        <div className="man9_container">
          <div className="man9_title">回款管理</div>
          <div className="man9_input">
            <Search placeholder="请输入回款编号" onSearch={onSearch} style={{ width: 300 }} />
          </div>
          <div className="man9_dropdown">
            <div className="money">
              <Button type="primary" onClick={() => setVisible(true)}>
                新建回款
                          </Button>
              <Modal
                title="新建回款"
                centered
                visible={visible}
                onOk={() => setVisible(false)}
                onCancel={() => setVisible(false)}
                width={700}
                okText='确定'
                cancelText='取消'
              >
                <div className="section_header">
                  <div className="section_mark"></div>
                  <div className="section_title">基本信息</div>
                </div>
                <div className="money_content">
                  <form className="money_box">
                    <div className="money_left">
                      <div style={{ color: "red", float: "left" }}>*</div>
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}>回款编号</div>
                      <div>
                        <Input style={{ width: "280px", height: "34px" }} />
                      </div>
                    </div>
                    <div className="money_right">
                      <div style={{ color: "red", float: "left" }}>*</div>
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}>客户名称</div>
                      <div>
                        <Input style={{ width: "280px", height: "34px" }} />
                      </div>
                    </div>
                    <div className="money_left">
                      <div style={{ color: "red", float: "left" }}>*</div>
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}>合同编号</div>
                      <div>
                        <Input style={{ width: "280px", height: "34px" }} />
                      </div>
                    </div>
                    <div className="money_right">
                      <div style={{ color: "red", float: "left" }}>*</div>
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}>回款日期</div>
                      <div>
                        <Space direction="vertical">
                          <DatePicker onChange={onChange} style={{ width: "280px", height: "34px" }} />
                        </Space>
                      </div>
                    </div>
                    <div className="money_left">
                      <div style={{ color: "red", float: "left" }}>*</div>
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}>回款方式</div>
                      <div>
                        <Select style={{ width: "280px", height: "40px" }} placeholder="请选择" onChange={handleProvinceChange}>
                          {provinceDhh.map(province => (
                            <Option key={province}>{province}</Option>
                          ))}
                        </Select>
                      </div>
                    </div>
                    <div className="money_right">
                      <div style={{ color: "red", float: "left" }}>*</div>
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px", float: "left" }}>合同金额</div>
                      <div style={{ fontSize: "12px", color: "rgb(153,153,153)", float: "left", marginLeft: "10px", marginTop: "5px" }}>(元)</div>
                      <div>
                        <Input style={{ width: "280px", height: "34px" }} />
                      </div>
                    </div>
                    <div className="money_left">
                      <div style={{ color: "red", float: "left" }}>*</div>
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}>期数</div>
                      <div>
                        <Input style={{ width: "280px", height: "34px" }} />
                      </div>
                    </div>
                    <div className="money_right">
                      <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}>备注</div>
                      <div>
                        <TextArea rows={4} style={{ width: "280px", height: "70px" }} />
                      </div>
                    </div>
                  </form>
                </div>
                <div className="money_section">
                  <div className="ms_header">
                    <div className="ms_mark"></div>
                    <div className="ms_title">基本信息</div>
                    <div className="ms_type">授权审批人</div>
                  </div>
                  <div className="ms_content">
                    <div style={{ color: "red", float: "left" }}>*</div>
                    <div style={{ display: "inline-block", fontSize: "12px", margin: "5px 0", paddingLeft: "3px" }}>审核人</div>
                    <div>
                      <Input style={{ width: "280px", height: "34px" }} />
                    </div>
                  </div>
                </div>
              </Modal>
            </div>
            <Space wrap>
              <Dropdown overlay={menu}>
                <Button>
                  更多 <DownOutlined />
                </Button>
              </Dropdown>
            </Space>
          </div>
        </div>
        <div className="man5_content">
          <div className="content_title">
            <div style={{ float: "left", paddingTop: "6px" }}>场景：</div>
            <div style={{ float: "left" }}>
              <Space wrap>
                <Dropdown overlay={menuu}>
                  <Button style={{ border: "none" }}>
                    全部回款 <DownOutlined />
                  </Button>
                </Dropdown>
              </Space>
            </div>
            <div>
              <Button type="primary" onClick={() => setVisiblea(true)} style={{ background: "none", border: "none", color: "#000", boxShadow: "0 0 0 0", fontSize: "14px" }}>
                <img src="data:image/png;base64," style={{ width: "12px", height: "13px", margin: "0 5px", verticalAlign: "middle" }} />
                    高级筛选
                </Button>
              <Modal
                title="高级筛选"
                centered
                visible={visiblea}
                onOk={() => setVisiblea(false)}
                onCancel={() => setVisiblea(false)}
                width={900}
                okText='确定'
                cancelText='取消'
              >
                <div style={{ marginBottom: "10px" }}>筛选条件</div>
                <Select style={{ width: "286px", height: "40px", float: "left" }} placeholder="请选择要筛选的字段名" onChange={handleProvinceChange}>
                  {provinceDa.map(province => (
                    <Option key={province}>{province}</Option>
                  ))}
                </Select>
                <div style={{ width: "35px", float: "left" }}>&nbsp;</div>
                <Select style={{ width: "143px", height: "40px", marginRight: "35px" }} defaultValue={provinceD[0]} onChange={handleProvinceChange}>
                  {provinceD.map(province => (
                    <Option key={province}>{province}</Option>
                  ))}
                </Select>
                <Input placeholder="请选择筛选条件" style={{ width: "280px", height: "40px" }} />
                <Space>
                  <Button onClick={showConfirm} style={{ border: "none" }}>
                    <CloseCircleOutlined />
                  </Button>
                </Space>
                <p>+添加筛选条件</p>
                <div style={{ marginTop: "10px" }}>
                  <div style={{ width: "14px", height: "14px", border: "1px #ddd solid", float: "left", borderRadius: "2px", marginTop: "4px" }}></div>
                  <div style={{ fontSize: "13px", lineHeight: "19px", paddingLeft: "8px", display: "inline-block" }}>保存为场景</div>
                </div>
              </Modal>
            </div>
          </div>
          <div className="content_data">
            <Table columns={columns} scroll={{ x: 2115 }} />
          </div>
          <div className="man4_footer">
            <Pagination size="small" total={50} showSizeChanger showQuickJumper />
          </div>
        </div>
      </div>
    </HashRouter>
  )
}
export default Man9